import React from 'react';
import { Row } from 'react-bootstrap';

function EditProdutoraStatic({
    initialValues,
    producerBankData
}) {
  return (
    <div >
    <div className="card-form pt-5 px-5 mb-5">
    <Row>
      <div className="col-md-12">
      <strong className="text-white">Email:</strong>
      <p className="text-white">{initialValues.email || '--'}</p>
      </div>
    </Row>
  </div>
  <div className="card-form pt-5 px-5 mb-5">
    <Row>
      <div className="col-md-5">
      <strong className="text-white">Nome:</strong>
      <p className="text-white">{initialValues.fullName || '--'}</p>
      </div>
      <div className="col-md-4">
      <strong className="text-white">Telefone:</strong>
      <p className="text-white">{initialValues.phone || '--'}</p>
      </div>
      <div className="col-md-3">
      <strong className="text-white">Data de Aniversário:</strong>
      <p className="text-white">{initialValues.birthday || '--'}</p>
      </div>
    </Row>
  </div>
      <div className="card-form pt-5 px-5 mb-5">
        <div className="col-md-9">
          <label className="text-white">Dados da Produtora</label>
        </div>
      </div>
      <div className="card-form pt-5 px-5 mb-5">
        <Row>
          <div className="col-md-5">
            <label className="text-white" htmlFor="category">Categoria</label>
            <p className="text-white">{initialValues.category && initialValues.category.label || '--'}</p>
          </div>
          <div className="col-md-7">
            <label className="text-white" htmlFor="description">Descrição</label>
            <p className="text-white">{initialValues.description || '--'}</p>
          </div>
          <div className="col-md-12">
            <label className="text-white" htmlFor="urlImage">Imagem</label>
            <p className="text-white">{initialValues.thumbnail || '--'}</p>
          </div>
        </Row>

      </div>
      <div className="card-form pt-5 px-5 mb-5">
        <Row>
          <div className="col-md-9">
            <label className="text-white">Redes Sociais</label>
          </div>
        </Row>
        <Row>
          <div className="col-md-12">
            <label className="text-white" htmlFor="rdInstagram">Instagram</label>
            <p className="text-white">{initialValues.rdInstagram || '--'}</p>
          </div>
          <div className="col-md-12">
            <label className="text-white" htmlFor="rdFacebook">Facebook</label>
            <p className="text-white">{initialValues.rdFacebook || '--'}</p>
          </div>
          <div className="col-md-12">
            <label className="text-white" htmlFor="rdSite">Site</label>
            <p className="text-white">{initialValues.rdSite || '--'}</p>
          </div>
        </Row>

      </div>
      <div className="card-form pt-5 px-5 mb-5">
        <Row>
          <div className="col-md-9">
            <label className="text-white">Dados Bancários</label>
          </div>
        </Row>
        {producerBankData.map((producer, index) => (
          <div className='' key={index}>
            <Row>
              <div className="col-md-3">
                <label className="text-white" htmlFor="bank">Banco</label>
                <p className="text-white">{producer.bank || '--'}</p>
              </div>
              <div className="col-md-4">
                <label className="text-white" htmlFor="bankBranch">Agência</label>
                <p className="text-white">{producer.bankBranch || '--'}</p>
              </div>
              <div className="col-md-5">
                <label className="text-white" htmlFor="account">Conta</label>
                <p className="text-white">{producer.account || '--'}</p>
              </div>
            </Row>
            <Row>
              <div className="col-md-3">
                <label className="text-white" htmlFor="pixType">Tipo de Pix</label>
                <p className="text-white">{producer.pixType || '--'}</p>
              </div>
              <div className="col-md-9">
                <label className="text-white" htmlFor="pixNumber">Chave Pix</label>
                <p className="text-white">{producer.pixNumber || '--'}</p>
              </div>
            </Row>
          </div>
        ))}
      </div>
      <div className="card-form pt-5 px-5 mb-5">
        <Row>
          <div className="col-md-5">
            <label className="text-white" htmlFor="cnpj">CNPJ</label>
            <p className="text-white">{initialValues.cnpj || '--'}</p>
          </div>
          <div className="col-md-7">
            <label className="text-white" htmlFor="companyName">Nome Comercial</label>
            <p className="text-white">{initialValues.companyName || '--'}</p>
          </div>
        </Row>

      </div>
  
  </div>
  );
}

export default EditProdutoraStatic;