import React, { useEffect, useState } from 'react';
import { Form } from 'react-bootstrap';
import Select from "react-select";
import HorizontalList from '../../components/HorizontalList';
import ShowByProfile from '../../components/ShowByProfile';
import { app } from '../../firebase_config';
import { useAuth } from '../../_contexts/auth';
import "./style.css"
import { SwiperSlide } from 'swiper/react';
import { Spinner } from 'reactstrap';
import { BiHeart, BiStar } from 'react-icons/bi';
import { MdAttachMoney, MdOutlineRemoveRedEye } from 'react-icons/md';
import { IoTicketOutline } from 'react-icons/io5';
import { useDispatch, useSelector } from 'react-redux';
import { clearValue, loadShowDash } from '../../store/ducks/shows/actions';
import { loadShowDashId } from '../../store/ducks/shows/actions';

function Dashboard() {
    const { userSigned } = useAuth();
    const dispatch = useDispatch();
    const { data, showSelected, loading, isActive } = useSelector((state) => state.shows);
    const [profileOptions, setProfileOptions] = useState([]);
    const [show, setShow] = useState(null);
    const [profileSelected, setprofileSelected] = useState(null);
    const customStyles = {
        option: (provided, state) => ({
            ...provided,
            color: state.isSelected ? 'white' : 'black',
            background: state.isSelected ? '#202020' : 'white',
            padding: 20,
        }),
        control: (base, state) => ({
            ...base,
            height: 46,
            borderRadius: 3,
            background: 'transparent',

        }),
        singleValue: (provided, state) => ({
            ...provided,
            color: 'white',
            transition: 'all 300ms',
            fontWeigth: 600
        }),
        multiValueRemove: (styles, { data }) => ({
            ...styles,
            backgroundColor: "white",
            color: '#353535',

            ':hover': {
                backgroundColor: "#353535",
                color: 'white',
            },
        }),
    };

    const getUsersByProfile = async (id) => {
        try {
            const res = await app.firestore().collection('users').where("profileId", "==", id && id).get();
            // const query = res.where("profileId", "==", id && id);
            let users = [];
            res.forEach(user => {
                users.push({ id: user.id, ...user.data() })
            });
            // const user = formatUsers(users)
            const formatedUsers = users.map(user =>
                ({ value: user.id, label: user.fullName.toUpperCase() })
            )
            setprofileSelected(formatedUsers[0])
            setProfileOptions(formatedUsers)


        } catch (err) {
            if (err.response) {
            }
            // console.log(err);
        }
    };

    console.log(profileOptions);

    useEffect(() => {
        //Get Lista de Produtoras
        if (userSigned && userSigned.profileId === '9421e035-1fd6-4918-8fc7-f4e865996bbc') {
            setprofileSelected({ value: userSigned.id, label: userSigned.fullName.toUpperCase() })
        } else {
            getUsersByProfile('9421e035-1fd6-4918-8fc7-f4e865996bbc');

        }
    }, [])

    useEffect(() => {

        if (profileSelected) {

            const profile = {
                producer_id: profileSelected.value
            }
            dispatch(loadShowDash(profile))
        }

    }, [profileSelected])

    useEffect(() => {
        if (profileSelected && data.length > 0) {
            setShow(data[0])
            dispatch(loadShowDashId(data[0].id))
        } else
            if (!profileSelected) {
                setShow(null)
                dispatch(clearValue())
            }

    }, [profileSelected, data])

    const handleChangeProfile = (data) => {
        setprofileSelected(data)
        const profile = {
            producer_id: data.value
        }
        dispatch(loadShowDash(profile))
    }

    const handleChangeShow = (data) => {
        setShow(data)
        dispatch(loadShowDashId(data.id))
    }

    return (
        <div className={`container list-shows ${data && profileSelected ? 'open' : ''}`}>
            <ShowByProfile role={['PRODUTORA']}>
                <h3 className="p-3 text-white">{userSigned !== null && userSigned.fullName}</h3>
            </ShowByProfile>
            <ShowByProfile role={['ADMIN']}>
                <Form>
                    <Form.Row>
                        <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12 p-3">
                            <label htmlFor="producer_id">Produtora</label>
                            <Select
                                id="producer_id"
                                name="producer_id"
                                type="text"
                                placeholder="Produtora"
                                className="react-select"
                                classNamePrefix="react-select"
                                styles={customStyles}
                                value={profileSelected && profileSelected}
                                options={profileOptions}
                                onChange={(data) => handleChangeProfile(data)}
                            />
                        </Form.Group>
                    </Form.Row>
                </Form>
            </ShowByProfile>
            <div className="container">
                <div className="row mb-4">
                    <div className="col-md-12 mb-2 text-center" >
                        <h4 className='title'>Faturamento Total</h4>
                    </div>
                    <div className="col-md-12 text-center" >
                        <h2 className='currencyHighLight'>R$ 3.009.852,00</h2>
                    </div>
                </div>
            </div>
            <div>
                <h5 className='text-white text-center'>Selecione um Espetáculo</h5>
                <div className={`list-shows ${data.length > 0 ? 'open' : ''}`}>
                    <HorizontalList id='horizontal-1'>
                        {!loading.loadShow && data.map((card, index) => (
                            <SwiperSlide key={String(index)} className='mr-4'>
                                {card === "" ? (
                                    <div />
                                ) : (
                                    <div className='card' onClick={() => handleChangeShow(card)}>
                                        <img
                                            src={card.thumbnail || '../../assets/logoPalcoweb.png'}
                                            style={{
                                                objectFit: 'cover',
                                                height: '225px',
                                                width: '175px',
                                                objectPosition: '10% 5%'
                                            }}
                                            onError={({ currentTarget }) => {
                                                currentTarget.onerror = null; // prevents looping
                                                currentTarget.src = "../../assets/logoPalcoweb.png";
                                            }}
                                            value={index}
                                            alt="imagem referente a peça"
                                        />
                                        <div className="overlay">{card.title}</div>
                                    </div>
                                )}
                            </SwiperSlide>
                        ))}
                    </HorizontalList>
                </div>
                {loading.loadShow && (
                    <div className="d-flex w-100 justify-content-center align-items-center">
                        <Spinner style={{ color: '#FFF' }} />
                    </div>
                )}

            </div>
            {showSelected && show ? (
                <div className="container">
                    <div className="row">
                        <div className="col-md-6 mb-3">
                            <div className="row d-flex align-items-stretch">
                                <div className="col-md-5 mb-3"><img src={show.thumbnail} className="img-fit" /></div>
                                <div className="col-md-7">
                                    <div className="row d-flex align-items-center gap-5">
                                        <span><BiStar /></span><span className='currency'>{showSelected.rating || 'Não foi votado'}</span><span className='text-muted'>classificação geral</span>
                                    </div>
                                    <div className="row d-flex align-items-center gap-5">
                                        <span><MdOutlineRemoveRedEye /></span><span className='currency'>{showSelected.views || 0}</span><span className='text-muted'>telespectadores</span>
                                    </div>
                                    <div className="row d-flex align-items-center gap-5">
                                        <span><BiHeart /></span><span className='currency'>{showSelected.like || 'Não foi favoritado'}</span><span className='text-muted'>vezes favoritado</span>
                                    </div>
                                    <div className="row d-flex align-items-center gap-5">
                                        <span><MdAttachMoney /></span><span className='currency'>{showSelected.ondemand || 0}</span><span className='text-muted'>lucro total</span>
                                    </div>
                                    {/* <div className="row d-flex align-items-center gap-5">
                                            <span><IoTicketOutline /></span><span className='currency'>208.047</span><span className='text-muted'>backstages vendidos</span>
                                        </div> */}
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="row">
                                <div className="col-md-12 text-center"><h5 className='text-white'>Faturamento Total do Espetáculo</h5></div>
                                <div className="col-md-12 text-center"><h2 className='currencyHighLight'>R$ 101.897,90</h2></div>
                            </div>
                        </div>

                    </div>
                </div>
            ) : !showSelected ? (
                <></>
            ) : loading.loadShowId && (
                <div className="d-flex w-100 justify-content-center align-items-center">
                    <Spinner style={{ color: '#FFF' }} />
                </div>
            )}


            {/* <div className="containerSubsSells">
            <div>
                <h4 style={{color: "#fff"}}>Inscritos</h4>
                <h1 style={{fontSize: "45px", color: "#FDCC12"}}>100.000</h1>
            </div>
            <div>
                <h4 style={{color: "#fff"}}>Vendas</h4>
                <h1 style={{fontSize: "45px", color: "#FDCC12"}}>R$ 100.000,00</h1>
            </div>
        </div>

        <div>
            <h4 className="subtitle">Espetáculos</h4>
            <div className="containerShows">
                <div className="cardInfoShow" >
                    <p >PEÇAS</p>
                    <h1 style={{margin: "10px", color: "#FDCC12"}}>1.000</h1>
                    <p>Ver Todas</p>
                </div>
                <div className="cardInfoShow">
                    <p>SHOWS</p>
                    <h1 style={{margin: "10px", color: "#FDCC12" }}>0</h1>
                    <p>Ver Todas</p>
                </div>
                <div className="cardInfoShow">
                    <div style={{color: "#fff"}}>PALESTRAS</div>
                    <h1 style={{margin: "10px", color: "#FDCC12"}}>0</h1>
                    <div style={{color: "#fff"}}>Ver Todas</div>
                </div>
            </div>
        </div>

        <div style={{display: "flex", width: "100%", justifyContent: "space-between", marginBottom: "45px" }}>
            <div style={{width: "40%" }}>
                <div><h4 style={{color: "#fff"}}>Adição de Conteúdo</h4></div>
            </div>
            <div style={{width: "40%" }}>
                <div><h4 style={{color: "#fff"}}>Adição por Categoria</h4></div>
            </div>
        </div> */}

        </div>
    );
}

export default Dashboard;