import React from 'react';
import { Modal as ModalCustom, Button } from 'react-bootstrap';


export function Modal({ children, modalTitle, setShow, setConfirm, buttonTitle, buttonColor, show }) {

    return (
        <ModalCustom
            show={show}
            onHide={setShow}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <ModalCustom.Header closeButton>
                <ModalCustom.Title>{modalTitle}</ModalCustom.Title>
            </ModalCustom.Header>
            <ModalCustom.Body>{children}</ModalCustom.Body>
            <ModalCustom.Footer>
                <Button variant="secondary" onClick={setShow}>
                    CANCELAR
                </Button>
                <Button variant="warning" className="d-flex font-weight-bold text-white" onClick={setConfirm}>
                    {buttonTitle}
                </Button>
            </ModalCustom.Footer>

        </ModalCustom>
    );
}