import { action } from 'typesafe-actions'
import {
  SHOWS_LOAD,
  SHOWS_LOAD_SUCCESS,
  SHOWS_LOAD_ERROR,
  SET_VALUE,
  SHOWS_CREATE,
  SHOWS_SUCCESS,
  SHOWS_ERROR,
  SHOWS_UPDATE,
  SHOWS_UPDATE_SUCCESS,
  SHOWS_UPDATE_ERROR,
  SHOWS_DELETE,
  SHOWS_DELETE_SUCCESS,
  SHOWS_DELETE_ERROR,
  CLEAR_VALUE,
  CATEGORY_SHOWS_LOAD,
  CATEGORY_SHOWS_LOAD_SUCCESS,
  CATEGORY_SHOWS_LOAD_ERROR,
  SCATEGORY_SHOWS_LOAD,
  SCATEGORY_SHOWS_LOAD_SUCCESS,
  SCATEGORY_SHOWS_LOAD_ERROR,
  SHOW_LOAD_ID,
  SHOW_LOAD_ID_SUCCESS,
  SHOW_LOAD_ID_ERROR,
  SHOWS_SETACTIVE,
  SHOWS_SETACTIVE_SUCCESS,
  SHOWS_SETACTIVE_ERROR,
  SHOWS_DASH_LOAD,
  SHOWS_DASH_LOAD_SUCCESS,
  SHOWS_DASH_LOAD_ERROR,
  SHOW_DASH_LOAD_ID,
  SHOW_DASH_LOAD_ID_SUCCESS,
  SHOW_DASH_LOAD_ID_ERROR,
} from './types'

export const loadShow = (data) => action(SHOWS_LOAD, { data })
export const loadShowSuccess = (data) => action(SHOWS_LOAD_SUCCESS, { data })
export const loadShowFailure = (error) => action(SHOWS_LOAD_ERROR, { error })

export const loadCategoryShow = () => action(CATEGORY_SHOWS_LOAD)
export const loadCategoryShowSuccess = (data) =>
  action(CATEGORY_SHOWS_LOAD_SUCCESS, { data })
export const loadCategoryShowFailure = (error) =>
  action(CATEGORY_SHOWS_LOAD_ERROR, { error })

export const loadSCategoryShow = (data) =>
  action(SCATEGORY_SHOWS_LOAD, { data })
export const loadSCategoryShowSuccess = (data) =>
  action(SCATEGORY_SHOWS_LOAD_SUCCESS, { data })
export const loadSCategoryShowFailure = (error) =>
  action(SCATEGORY_SHOWS_LOAD_ERROR, { error })

export const loadShowId = (data) => action(SHOW_LOAD_ID, { data })
export const loadShowIdSuccess = (data) =>
  action(SHOW_LOAD_ID_SUCCESS, { data })
export const loadShowIdFailure = (error) =>
  action(SHOW_LOAD_ID_ERROR, { error })

export const setValue = (payload) => action(SET_VALUE, { payload })
export const clearValue = () => action(CLEAR_VALUE)

export const createShow = (data) => action(SHOWS_CREATE, { data })
export const createShowSuccess = (status) => action(SHOWS_SUCCESS, { status })
export const createShowFailure = (error) => action(SHOWS_ERROR, { error })

export const updateShow = (data) => action(SHOWS_UPDATE, { data })
export const updateShowSuccess = (status) =>
  action(SHOWS_UPDATE_SUCCESS, { status })
export const updateShowFailure = (error) =>
  action(SHOWS_UPDATE_ERROR, { error })

export const deleteShow = (data) => action(SHOWS_DELETE, { data })
export const deleteShowSuccess = (status) =>
  action(SHOWS_DELETE_SUCCESS, { status })
export const deleteShowFailure = (error) =>
  action(SHOWS_DELETE_ERROR, { error })

export const updateSetShow = (data) => action(SHOWS_SETACTIVE, { data })
export const updateSetShowSuccess = (active) =>
  action(SHOWS_SETACTIVE_SUCCESS, { active })
export const updateSetShowFailure = (error) =>
  action(SHOWS_SETACTIVE_ERROR, { error })


export const loadShowDash = (data) => action(SHOWS_DASH_LOAD, { data })
export const loadShowDashSuccess = (data) => action(SHOWS_DASH_LOAD_SUCCESS, { data })
export const loadShowDashFailure = (error) => action(SHOWS_DASH_LOAD_ERROR, { error })

export const loadShowDashId = (data) => action(SHOW_DASH_LOAD_ID, { data })
export const loadShowDashIdSuccess = (data) =>
  action(SHOW_DASH_LOAD_ID_SUCCESS, { data })
export const loadShowDashIdFailure = (error) =>
  action(SHOW_DASH_LOAD_ID_ERROR, { error })
