import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Modal } from '../../../components/Modal';
import { deletePlan, setValue } from '../../../store/ducks/plans/actions';

const DeleteForm = () => {
    const dispatch = useDispatch();
    const { planModalDelete, planSelected, loading } = useSelector((state) => state.plans);
    const handleClose = () => {
        dispatch(setValue({ planModalDelete: false }));
    }
    const handleDelete = () => {
        dispatch(deletePlan(planSelected.id));
        handleClose()
    }
    return (
        <Modal
            modalTitle="Deletar Usuário"
            buttonTitle="Confirmar"
            buttonColor="primary"
            show={planModalDelete}
            setShow={handleClose}
            setConfirm={handleDelete}
        >
            <p className="text-white font-weight-bold">Deseja realmente excluir o Plano</p>
        </Modal>
    );
}

export default DeleteForm;