import React from "react";
import { Form, FormCheck } from "react-bootstrap";
import { Input } from "reactstrap";

const CkeckBox = ({
	input,
	placeholder,
	type,
	label,
	id,
	disabled,
	name,
	meta: { touched, error },
	...rest
}) => {
	return (
		<Form.Group>
			<FormCheck {...input} {...rest} placeholder={placeholder} type="checkbox" disabled={disabled}
				label={label} />
			{touched && error && (
				<div className={`tooltip-error ${error ? 'active-tooltip' : ''}`}>{error}</div>
			)}
		</Form.Group>
	);
};


export default CkeckBox;