import { call, put, take } from 'redux-saga/effects'
import { formatShow, formatShows } from '../../../helpers/dtos/shows';
import { status } from '../../../helpers/http-status/http-status'
import { convertToFloat } from '../../../helpers/utils';
import api from '../../../services'
import * as actions from './actions'

export function* loadShow({ payload }) {
  const {search, userId} = payload.data;
  console.log(userId);

  try {
    const { data } = yield call(
      api.get,
      `/manager/spectacle/list${search ? `?search=${search}` : ''}`
    )
    const shows = formatShows(data.body);
    if(userId){
      const dataByProdutora = shows.filter(show=>show.producer_id===userId)

      yield put(actions.loadShowSuccess(dataByProdutora))

    }else{
    yield put(actions.loadShowSuccess(shows))

    }

  } catch (err) {
    if (err.response) {
      yield put(actions.loadShowFailure(status[err.response.status]))
    } else {
      yield put(actions.loadShowFailure(status['default']))
    }
  }
}

