export function* loadShowDash({ payload }) {
  const {search, producer_id} = payload.data;

  try {
    const { data } = yield call(
      api.get,
      `/manager/spectacle/producer/list?producer_id=${producer_id}${search ? `&?search=${search}` : ''}`
    )
    const shows = formatShows(data.body);

    yield put(actions.loadShowDashSuccess(shows))

  } catch (err) {
    if (err.response) {
      yield put(actions.loadShowDashFailure(status[err.response.status]))
    } else {
      yield put(actions.loadShowDashFailure(status['default']))
    }
  }
}

