import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Collapse } from 'react-bootstrap';
import { Trans } from 'react-i18next';
import { FiUsers } from 'react-icons/fi';
import { FaTheaterMasks } from 'react-icons/fa';
import { BiListCheck } from 'react-icons/bi';
import ShowByProfile from '../components/ShowByProfile';

class Sidebar extends Component {
  state = {};

  toggleMenuState(menuState) {
    if (this.state[menuState]) {
      this.setState({ [menuState]: false });
    } else if (Object.keys(this.state).length === 0) {
      this.setState({ [menuState]: true });
    } else {
      Object.keys(this.state).forEach(i => {
        this.setState({ [i]: false });
      });
      this.setState({ [menuState]: true });
    }
  }

  componentDidUpdate(prevProps) {

    if (this.props.location !== prevProps.location) {
      this.onRouteChanged();
    }
  }

  onRouteChanged() {
    document.querySelector('#sidebar').classList.remove('active');
    Object.keys(this.state).forEach(i => {
      this.setState({ [i]: false });
    });

    const dropdownPaths = [
      { path: '/dashboard', state: 'dashboard' },
      { path: '/users', state: 'users' },
      { path: '/plans', state: 'plans' },
      { path: '/shows', state: 'shows' },
      { path: '/tables', state: 'tablesMenuOpen' },
      { path: '/maps', state: 'mapsMenuOpen' },
      { path: '/icons', state: 'iconsMenuOpen' },
      { path: '/charts', state: 'chartsMenuOpen' },
      { path: '/user-pages', state: 'userPagesMenuOpen' },
      { path: '/error-pages', state: 'errorPagesMenuOpen' },
      { path: '/general-pages', state: 'generalPagesMenuOpen' },
      { path: '/ecommerce', state: 'ecommercePagesMenuOpen' },
    ];

    dropdownPaths.forEach((obj => {
      if (this.isPathActive(obj.path)) {
        this.setState({ [obj.state]: true })
      }
    }));

  }
  render() {

    return (
      <nav className="sidebar sidebar-offcanvas" id="sidebar">
        <ul className="nav">
          <li className={this.isPathActive('/dashboard') ? 'nav-item active' : 'nav-item'}>
            <Link className="nav-link" to="/dashboard">
              <span className="icon-bg"><i className="mdi mdi-cube menu-icon text-white"></i></span>
              <span className="menu-title">Dashboard</span>
            </Link>
          </li>
          <li className={this.isPathActive('/shows') ? 'nav-item active' : 'nav-item'}>
            <div className={this.state.shows ? 'nav-link menu-expanded' : 'nav-link'} onClick={() => this.toggleMenuState('shows')} data-toggle="collapse">
              <span className="mr-2"><FaTheaterMasks /></span>
              <span className="menu-title">Espetáculos</span>
              <i className="menu-arrow"></i>
            </div>
            <Collapse in={this.state.shows}>
              <ul className="nav flex-column sub-menu">
                <li className="nav-item"> <Link className={this.isPathActive('/shows') ? 'nav-link active' : 'nav-link'} to="/shows">Listagem</Link></li>
                <li className="nav-item"> <Link className={this.isPathActive('/shows/create') ? 'nav-link active' : 'nav-link'} to="/shows/create">Cadastro</Link></li>
              </ul>
            </Collapse>
          </li>
          <ShowByProfile role={['ADMIN']}>
          <li className={this.isPathActive('/plans') ? 'nav-item active' : 'nav-item'}>
            <div className={this.state.plains ? 'nav-link menu-expanded' : 'nav-link'} onClick={() => this.toggleMenuState('plans')} data-toggle="collapse">
              <span className="mr-2"><BiListCheck /></span>
              <span className="menu-title">Planos</span>
              <i className="menu-arrow"></i>
            </div>
            <Collapse in={this.state.plans}>
              <ul className="nav flex-column sub-menu">
                <li className="nav-item"> <Link className={this.isPathActive('/plans') ? 'nav-link active' : 'nav-link'} to="/plans">Listagem</Link></li>
                <li className="nav-item"> <Link className={this.isPathActive('/plans/create') ? 'nav-link active' : 'nav-link'} to="/plans/create">Cadastro</Link></li>
              </ul>
            </Collapse>
          </li>
          </ShowByProfile>
          <ShowByProfile role={['ADMIN']}>
          <li className={this.isPathActive('/users') ? 'nav-item active' : 'nav-item'}>
            <div className={this.state.users ? 'nav-link menu-expanded' : 'nav-link'} onClick={() => this.toggleMenuState('users')} data-toggle="collapse">
              <span className="mr-2"><FiUsers /></span>
              <span className="menu-title">Usuários</span>
              <i className="menu-arrow"></i>
            </div>
            <Collapse in={this.state.users}>
              <ul className="nav flex-column sub-menu">
                <li className="nav-item"> <Link className={this.isPathActive('/users') ? 'nav-link active' : 'nav-link'} to="/users">Listagem</Link></li>
                <li className="nav-item"> <Link className={this.isPathActive('/users/create') ? 'nav-link active' : 'nav-link'} to="/users/create">Cadastro</Link></li>
              </ul>
            </Collapse>
          </li>
          </ShowByProfile>
          <li className="nav-item sidebar-user-actions">
            <div className="sidebar-user-menu">
              <a href='/' onClick={event => this.props.signOut()} className="nav-link"><i className="mdi mdi-logout menu-icon"></i>
                <span className="menu-title"><Trans>Sair da conta</Trans></span></a>
            </div>
          </li>
        </ul>
      </nav>
    );
  }

  isPathActive(path) {
    return this.props.location.pathname.startsWith(path);
  }

  componentDidMount() {
    this.onRouteChanged();
    // add className 'hover-open' to sidebar navitem while hover in sidebar-icon-only menu
    const body = document.querySelector('body');
    document.querySelectorAll('.sidebar .nav-item').forEach((el) => {

      el.addEventListener('mouseover', function () {
        if (body.classList.contains('sidebar-icon-only')) {
          el.classList.add('hover-open');
        }
      });
      el.addEventListener('mouseout', function () {
        if (body.classList.contains('sidebar-icon-only')) {
          el.classList.remove('hover-open');
        }
      });
    });
  }

}

export default Sidebar;