import { action } from 'typesafe-actions'
import {
  CREATE_USER,
  CREATE_USER_ERROR,
  CREATE_USER_SUCCESS,
  LOAD_USER, LOAD_USER_ERROR, LOAD_USER_SUCCESS, SET_VALUE
} from './types'

export const setValue = (payload) => action(SET_VALUE, { payload })


export const loadUserRequest = () => action(LOAD_USER)

export const loadUserSuccess = (users) => action(LOAD_USER_SUCCESS, { users })

export const loadUserFailure = () => action(LOAD_USER_ERROR)


export const createUserRequest = () => action(CREATE_USER)

export const createUserSuccess = () => action(CREATE_USER_SUCCESS)

export const createUserFailure = () => action(CREATE_USER_ERROR)