import React, { useEffect } from "react";
import { Button, ButtonGroup } from "react-bootstrap";
import { MdOutlineDeleteOutline, MdOutlineEdit } from 'react-icons/md';
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { Col, Row } from "reactstrap";
import TableCustom from "../../components/Table";
import { setValue, loadPlan } from "../../store/ducks/plans/actions";
import DeleteForm from "./components/DeleteForm";
import HeaderList from "./components/HeaderList";


function Report() {
	const history = useHistory();
	const dispatch = useDispatch();
	const { data, error, loading } = useSelector((state) => state.plans);

	const handleDelete = (plan) => {
		dispatch(setValue({ planModalDelete: true, planSelected: plan }));
	}

	const handleEdit = (plan) => {
		history.push(`/plans/edit/${plan.id}`)
	}
	const columns =
		[
			{
				Header: "Nome",
				accessor: "name"
			},
			{
				Header: "Valor",
				accessor: "value"
			},
			{
				Header: "Ativo",
				accessor: "isActive"
			},
			{
				Header: 'Ações',
				cell: (plan) => {
					return (
						<ButtonGroup>
							<Button
								variant="secondary"

								onClick={() => handleEdit(plan)}
								className="d-flex font-weight-bold text-white btn btn-xs"
							>
								<MdOutlineEdit className="text-white font-weight-bold mr-1" />
								<span className="text-white font-weight-bold mr-1">
									Editar
								</span>
							</Button>
							<Button
								variant="warning"
								onClick={() => handleDelete(plan)}
								className="d-flex text-black font-weight-bold btn btn-xs"
							>
								<MdOutlineDeleteOutline className="text-black font-weight-bold mr-1" />
								<span className="text-black font-weight-bold mr-1">
									Excluir
								</span>
							</Button>
						</ButtonGroup>
					);
				},
			},
		]
	useEffect(() => dispatch(loadPlan()), [])


	return (
		<div className="dashboard">
			<Row>
				<Col md={12} className="table-responsive col-md-12">
					<HeaderList />

					<TableCustom
						title="Usuários"
						data={data}
						loading={loading}
						columns={columns}
					/>
				</Col>
			</Row>
			<DeleteForm />
		</div>

	);
}

export default Report;