import React, { useState } from 'react';

import { GrFormSearch } from 'react-icons/gr';
import { FaTheaterMasks } from 'react-icons/fa';
import { Button } from 'react-bootstrap';
import { useHistory } from 'react-router';
import { useDispatch } from 'react-redux';
import { loadShow } from '../../../store/ducks/shows/actions';
import profiles from '../../../helpers/models/profiles';

function HeaderList() {
    const history = useHistory();
    const dispatch = useDispatch();
    const [search, setSearch] = useState('');
    const logged_user = JSON.parse(localStorage.getItem('@PermissionYT:user'));
	const isProdutora = profiles[logged_user.profileId].toUpperCase()==='PRODUTORA';

    return (
        <div className="container px-20 m-0 mb-5">
            <div className="row justify-content-between w-100 p-0 m-0">

                <div className="col-sm-12 col-md-5">
                    <h2 className="text-white title-header-table">Gerenciamento de Espetáculos</h2>
                </div>

                <div className="col-sm-12 col-md-4">
                    <div className="input-group h2">
                        <input name="data[search]" className="form-control" id="search" type="text" placeholder="Pesquisar Itens" onChange={(e) => setSearch(e.target.value)} />
                        <span className="input-group-prepend">
                            <button className="btn btn-primary" type="button" onClick={() => dispatch(loadShow({search:search, userId: isProdutora && logged_user.id}))}>
                                <span className="glyphicon-search text-white">
                                    <GrFormSearch />
                                </span>
                            </button>
                        </span>
                    </div>
                </div>

                <div className="col-sm-12 col-md-3">
                    <Button className="btn btn-primary w-100 h2 text-nowrap" onClick={() => history.push('/shows/create')}>
                        <FaTheaterMasks className="text-black mr-2" />
                        <span className="text-black font-weight-bold">Cadastrar Espetáculo</span>
                    </Button>
                </div>
            </div>
        </div>
    );
}

export default HeaderList;