const validate = (obj, fields) => {
  // obj = {fieldName:'value'}
  // fields = [{name: 'field1', typeValidation: ['required']}]

  let errors = {}
  fields.forEach((field) => {
    if (field.typeValidation.find((type) => type === 'required')) {
      if (field.typeField === 'text') {
        if (!obj[field.name]) {
          errors[field.name] = 'Campo obrigatório'
        }
      } else if (field.typeField === 'select') {
        if (!(obj[field.name] && obj[field.name].value)) {
          errors[field.name] = 'Selecione uma opção'
        }
      }
    }
  })
  return errors
}

export default validate
