import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { app } from '../../firebase_config';
import profiles from '../../helpers/models/profiles';
import { loadCategoryShow } from '../../store/ducks/shows/actions';
import { useToast } from '../../_contexts/toast';
import EditProdutoraForm from './components/EditProdutoraForm';

function EditUsers() {
  const dispatch = useDispatch();
  const [userSelected, setUserSelected] = useState(null)
  const [error, setError] = useState([])
  const [loading, setLoading] = useState(false)
  const [isChangePass, setIsChangePass] = useState(false)
  const { showToast } = useToast();
  const [profileSelected, setProfileSelected] = useState(null);
  const [producerBankData, setProducerBankData] = useState([{
    bank: "",
    bankBranch: "",
    account: "",
    pixType: "",
    pixNumber: "",
  }]);
  const { categories } = useSelector((state) => state.shows);
  const history = useHistory();
  const { id } = useParams();

  useEffect(() => {
    dispatch(loadCategoryShow())
  }, [])

  useEffect(() => {
    getData()
  }, [])

  const getData = () => {
    app.firestore().collection("users").doc(id).get()
      .then(res => {
        const user = res.data();

        setUserSelected(user)
        if (user.profileId === '9421e035-1fd6-4918-8fc7-f4e865996bbc') {
          setProducer(user);
          setProfile(user.profileId)
        }

      });
  }

  const onSubmit = async data => {
    data.profileId = data.profileId.value
    data.birthday = data.birthday === "Invalid date" ? "" : moment(data.birthday).format('YYYYMMDD');
    if (userSelected.profileId === '9421e035-1fd6-4918-8fc7-f4e865996bbc' && data.profileId !== '9421e035-1fd6-4918-8fc7-f4e865996bbc') {
      console.log('eu era produtora');
      delete data.corporateData
      delete data.description
      delete data.producerBankData
      delete data.socialNetwork
      delete data.status
      delete data.thumbnail
      delete data.thumbnailGrande
      delete data.category
    }
    if (isChangePass) {
      //   //TODO: signupUserLogin(obj, cb)
    } else {
      data.email = userSelected.email;
      data.password = userSelected.password;

      console.log('eu era qualquer coisa');
      console.log(data, 'data nova');
      return;
      signupUserPessoais(data)
    }

  };

  const signupUserPessoais = (userDetails) => {
    setLoading(true)

    app.firestore().collection('users').doc(id)
      .set(userDetails, { merge: true }).then(res => {
        setLoading(false)
        showToast('success', 'Dados pessoais atualizados com sucesso');
        history.goBack()
      })
      .catch(error => {
        console.log(error);
        setLoading(false)
        setError(error)
        showToast('error', 'Ooops algo deu errado ')

      })


  };


  const categoriesOptions = categories.length ? categories?.map(category => (
    { value: category.id, label: category.name.toUpperCase() }
  )) : [{ value: null, label: 'Selecione uma categoria' }]

  const profilesOptions = Object.keys(profiles).map(profile => (
    { value: profile, label: profiles[profile] }
  ))

  const handleProfileSelected = (e) => {
    setProfileSelected(e);
  }

  const handleProducerBank = (e, index) => {
    let arrCopy = [...producerBankData];
    const { name, value } = e.target;
    arrCopy[index][name] = value;
    setProducerBankData(arrCopy);
  }

  const addProducerBank = () => {
    if (producerBankData.length === 5) {
      return;
    }
    setProducerBankData([...producerBankData, {
      "bank": "",
      "bankBranch": "",
      "account": "",
      "pixType": "",
      "pixNumber": "",
    }]);
  }

  const setProducer = (user) => {
    setProducerBankData([...user.producerBankData])
  }

  const setProfile = (profileId) => {
    setProfileSelected(profilesOptions.find(profile => profile.value === profileId))
  }

  const changeEmailPass = (e) => {
    setIsChangePass(e)
  }

  const initialValues = userSelected && {
    ...userSelected,
    birthday: userSelected.birthday ? moment(userSelected.birthday).format('YYYY-DD-MM') : '',
    fullName: userSelected.fullName,
    phone: userSelected.phone,
    category: categoriesOptions.find(category => category.id === userSelected.category),
    profileId: profilesOptions.find(profile => profile.value === userSelected.profileId)
  }

  return (
    <div className="dashboard align-items-center px-0 lock-full-bg w-80 h-80">
      <div className="row w-100 mx-0">
        <div className="col-md-12 col-lg-12 mx-auto">
          <div className="container">
            <h1 className="display-4 text-white">Editar Usuário</h1>
          </div>
          <EditProdutoraForm
            onSubmit={onSubmit}
            loading={loading}
            profilesOptions={profilesOptions}
            categoriesOptions={categoriesOptions}
            handleProfileSelected={handleProfileSelected}
            profileSelected={profileSelected}
            handleProducerBank={handleProducerBank}
            producerBankData={producerBankData}
            addProducerBank={addProducerBank}
            initialValues={initialValues}
            changeEmailPass={changeEmailPass}
            isChangePass={isChangePass}
          />
        </div>
      </div>
    </div>
  );
}

export default EditUsers
