import React from "react";
import { Form } from "react-bootstrap";

const Input = ({
	input,
	placeholder,
	type,
	meta: { touched, error },
	...rest
}) => {
	return (
		<Form.Group>
			<Form.Control {...input} {...rest} placeholder={placeholder} type={type} autoComplete="new-off" />
			{touched && error && (
				<div className={`tooltip-error ${error ? 'active-tooltip' : ''}`}>{error}</div>
			)}
		</Form.Group>
	);
};


export default Input;