import active from "../models/active"
import { currency } from "../utils"

export const formatPlans = (items) => {
    const plans = items.map(item => ({
        ...item,
        value: currency(item.value),
        isActive: active[item.isActive]
    }))
    return plans
}

export const formatPlan = (item) => {
    const plans = {
        ...item,
        value: currency(item.value),
        isActive: active[item.isActive]
    }
    return plans
}