import { call, put, take } from 'redux-saga/effects'
import { formatPlan, formatPlans } from '../../../helpers/dtos/plans'
import { status } from '../../../helpers/http-status/http-status'
import api from '../../../services'
import * as actions from './actions'

export function* loadPlan() {
  try {
    const { data } = yield call(api.get, 'plans/list')
    const plans = formatPlans(data.body)
    yield put(actions.loadPlanSuccess(plans))
  } catch (err) {
    if (err.response) {
      yield put(actions.loadPlanFailure(status[err.response.status]))
    } else {
      yield put(actions.loadPlanFailure(status['default']))
    }
  }
}

export function* loadPlanId({ payload }) {
  console.log(payload)
  try {
    const { data } = yield call(api.get, `/manager/plans?id=${payload.data}`) // Rota Api
    const plans = formatPlan(data.body)
    yield put(actions.loadPlanIdSuccess(plans))
  } catch (err) {
    if (err.response) {
      yield put(actions.loadPlanIdFailure(status[err.response.status]))
    } else {
      yield put(actions.loadPlanIdFailure(status['default']))
    }
  }
}

export function* createPlan({ payload }) {
  try {
    const resp = yield call(api.post, `manager/plans`, payload.data)
    console.log('create', resp)
    yield put(actions.createPlanSuccess(status[resp.status]))
    try {
      yield take(actions.createPlanSuccess())
      const { data } = yield call(api.get, 'plans/list')
      yield put(actions.loadPlanSuccess(data))
    } catch (error) {
      if (error.response) {
        yield put(actions.loadPlanFailure(status[error.response.status]))
      } else {
        yield put(actions.loadPlanFailure(status['default']))
      }
    }
  } catch (error) {
    if (error.response) {
      yield put(actions.createPlanFailure(status[error.response.status]))
    } else {
      yield put(actions.createPlanFailure(status['default']))
    }
  }
}

export function* updatePlan({ payload }) {
  const { id,is_test_period,period, ...rest } = payload.data
  try {
    const resp = yield call(api.put, `manager/plans/${id}`, rest)
    yield put(actions.updatePlanSuccess(status[resp.status]))
    try {
      yield take(actions.updatePlanSuccess())
      const { data } = yield call(api.get, 'plans/list')
      yield put(actions.updatePlanSuccess(data))
    } catch (error) {
      if (error.response) {
        yield put(actions.loadPlanFailure(status[error.response.status]))
      } else {
        yield put(actions.loadPlanFailure(status['default']))
      }
    }
  } catch (error) {
    if (error.response) {
      yield put(actions.updatePlanFailure(status[error.response.status]))
    } else {
      yield put(actions.updatePlanFailure(status['default']))
    }
  }
}

export function* deletePlan({ payload }) {
  try {
    yield call(api.delete, `manager/plans/${payload.data}`)
    const { data } = yield call(api.get, 'plans/list')
    const plans = formatPlans(data.body)
    yield put(actions.deletePlanSuccess(plans))
  } catch (error) {
    if (error.response) {
      yield put(actions.deletePlanFailure(status[error.response.status]))
    } else {
      yield put(actions.deletePlanFailure(status['default']))
    }
  }
}
