import React, { Component } from 'react';
import { Dropdown } from 'react-bootstrap';
import { Trans } from 'react-i18next';
import { FaUserCircle } from "react-icons/fa";
import ShowByProfile from '../components/ShowByProfile';
import ShowProfile from '../components/ShowProfile';

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {activeProfile: false};
    this.toggleProfile = this.toggleProfile.bind(this);
  }
  toggleOffcanvas() {
    document.querySelector('.sidebar-offcanvas').classList.toggle('active');
  }
  toggleProfile() {
    this.setState(prevState => ({
      activeProfile: !prevState.activeProfile
    }));
  }
  render() {

    return (
      <nav className="navbar navbar-transparent default-layout-navbar col-lg-12 col-12 p-0 fixed-top d-flex flex-row">
        <div className="text-center navbar-brand-wrapper d-flex align-items-center justify-content-center">
          {/* <Link className="navbar-brand brand-logo" to="/"><img src={require('../../assets/images/logo.svg')} alt="logo" /></Link> */}
          {/* <Link className="navbar-brand brand-logo-mini" to="/"><img src={require('../../assets/images/logo-mini.svg')} alt="logo" /></Link> */}
        </div>
        <div className="navbar-menu-wrapper d-flex align-items-stretch">
          <button className="navbar-toggler navbar-toggler align-self-center" type="button" onClick={() => document.body.classList.toggle('sidebar-icon-only')}>
            <span className="mdi mdi-menu"></span>
          </button>

          <ul className="navbar-nav navbar-nav-right">
            <li className="nav-item nav-profile nav-language">

              <Dropdown alignRight id="profile-menu" onClick={this.toggleProfile}>
                <Dropdown.Toggle className="nav-link count-indicator">
                  <div className="nav-profile-img">
                    <FaUserCircle size={30} color='white'/>
                    {/* <img src={require("../../assets/images/faces/face28.png")} alt="profile" /> */}
                  </div>
                  <div className="nav-profile-text">
                    <p className="mb-1 text-muted">{this.props.userSigned !== null && this.props.userSigned.fullName}</p>
                    <ShowProfile profileId={this.props.userSigned !== null && this.props.userSigned.profileId}></ShowProfile>
                  </div>
                </Dropdown.Toggle>
                <Dropdown.Menu className="preview-list navbar-dropdown" style={{display: this.state.activeProfile?'block':'none'}} >
                  <div className="p-2">
                    <ShowByProfile role={['PRODUTORA']}>
                    <Dropdown.Item className="dropdown-item d-flex align-items-center justify-content-between" onClick={evt => {evt.preventDefault(); this.props.history.push('/personal-data')}}>
                      <span><Trans>Meus Dados</Trans></span>
                      <span className="p-0">
                        <i className="mdi  mdi-account-outline ml-1"></i>
                      </span>
                    </Dropdown.Item>
                    </ShowByProfile>
                    <Dropdown.Item className="dropdown-item d-flex align-items-center justify-content-between" href="/" onClick={evt => {this.props.signOut()}}>
                      <span><Trans>Sair da conta</Trans></span>
                      <span className="p-0">
                        <i className="mdi mdi-logout ml-1"></i>
                      </span>
                    </Dropdown.Item>
                  </div>
                </Dropdown.Menu>
              </Dropdown>
            </li>
          </ul>
          <button className="navbar-toggler navbar-toggler-right d-lg-none align-self-center" type="button" onClick={this.toggleOffcanvas}>
            <span className="mdi mdi-menu"></span>
          </button>
        </div>
      </nav>
    );
  }
}

export default Navbar;
