import React from 'react';


function Footer() {
  const year = new Date().getFullYear();
  return (
    <footer className="footer">
    <div className="container-fluid">
      <div className="d-sm-flex justify-content-center py-2 w-100">
        <span className="text-muted text-center d-block d-sm-inline-block">Copyright © <a href="https://www.palcoweb.com.br/" target="_blank" rel="noopener noreferrer">palcoweb.com.br </a>{year && year}</span>
      </div>
    </div>
  </footer>
  );
}

export default Footer;