import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Modal } from '../../../components/Modal';
import { app } from '../../../firebase_config';
import { setValue } from '../../../store/ducks/users/actions';
import { useToast } from '../../../_contexts/toast';

// import { Container } from './styles';

const DeleteForm = () => {
    const dispatch = useDispatch();
    const { userSelected, showModalDelete } = useSelector((state) => state.users);
    const { showToast } = useToast();
    if (userSelected === null) return null;
    console.log(userSelected)
    const handleDelete = () => {
        const user = app.auth().currentUser;

        user.delete().then(() => {
            showToast('success', 'Usuário deletado com sucesso')
            handleClose()
        }).catch((error) => {
            console.log(error);
        });
        handleClose()
    }

    const handleClose = () => {
        dispatch(setValue({ showModalDelete: false }))
    }
    return (
        <Modal
            modalTitle="Deletar Usuário"
            buttonTitle="Confirmar"
            buttonColor="primary"
            show={showModalDelete}
            setShow={handleClose}
            setConfirm={handleDelete}
        >
            <p className="text-white font-weight-bold">Deseja realmente excluir o usuário <span className="font-weight-bold">{userSelected.fullName}</span></p>
        </Modal>
    );
}

export default DeleteForm;