import React from 'react';
import { Col, Row, Spinner, Table, Container } from 'reactstrap';
import { v4 as uuid } from 'uuid';

function TableCustom({
    data,
    noData,
    loading,
    columns,
    title,
}) {
    return (
        data && (
            <div id="list" className="row">
                <div className="table-responsive col-md-12">
                    <Table striped dark>
                        <thead>
                            <tr>
                                {
                                    columns.map(col => (
                                        <th key={`header-${uuid()}`}>{col.Header}</th>
                                    ))
                                }
                            </tr>
                        </thead>


                        <tbody className="bg-white text-black">

                            {data.length > 0 && !loading &&
                                data.map(row => (
                                    <tr key={`${row}-${uuid()}`}>
                                        {
                                            columns.map((col) => (
                                                col.accessor ? (
                                                    <td key={`cell-${uuid()}`}>
                                                        {row[col.accessor]}
                                                    </td>
                                                ) :
                                                    (
                                                        <td key={`cell-${uuid()}`}>
                                                            {col.cell(row)}
                                                        </td>
                                                    )
                                            ))
                                        }
                                    </tr>
                                ))}

                        </tbody>

                    </Table>

                    {data.length == 0 && !loading
                        ? (
                            <div className="d-flex w-100 justify-content-center align-items-center" style={{ minHeight: '60vh', background: 'white' }}>
                                <p className="text-black">{noData}</p>
                            </div>

                        ) : loading && (
                            <div className="d-flex w-100 justify-content-center align-items-center" style={{ minHeight: '60vh', background: 'white' }}>
                                <Spinner />
                            </div>
                        )}

                </div>
            </div>
        )
    );
}

export default TableCustom;