import React from 'react';
import { Button, Form } from 'react-bootstrap';
import { Field, reduxForm } from "redux-form";
import InputCustom from '../../../components/Input';
import { MONEY } from '../../../components/NumberFormat';
import CheckBox from '../../../components/CkeckBox';
import Spinner from '../../../Layout/Spinner';
import validate from './validate';

function CreateForm({
  handleSubmit,
  reset,
  loading,
  initialValues
}) {
  return (
    <Form autoComplete="new-off" className="form" onSubmit={handleSubmit} >
      <div className="card-form pt-5 px-5 mb-5">
      <Form.Row>
        <Form.Group className="col-md-6">
          <label htmlFor="name">Nome</label>
          <Field
            id="name"
            name="name"
            component={InputCustom}
            className="form-control"
            type="text"
            placeholder="Nome do Plano"
          />
        </Form.Group>
        <Form.Group className="col-md-6">
          <label htmlFor="value">Valor</label>
          <Field
            id="value"
            name="value"
            component={MONEY}
            className="form-control"
            type="text"
            placeholder="Valor do Plano"
          />
        </Form.Group>
      </Form.Row>
      <Form.Row className='d-flex align-items-center'>
      <Form.Group className="col-md-4" >
      <label htmlFor="name">Periodo</label>
            <Field
            id="period"
            name="period"
            component={InputCustom}
            className="form-control"
            type="number"
            placeholder="Informe um periodo em dias"
            />
          </Form.Group>
          <Form.Group className="col-md-4" >
      <label htmlFor="name">Ordem</label>
            <Field
            id="order"
            name="order"
            component={InputCustom}
            className="form-control"
            type="number"
            placeholder="Informe a ordenação"
            />
          </Form.Group>
      <Form.Group className="col-md-4" >
            <Field
              id="is_test_period"
              name="is_test_period"
              checked={initialValues.is_test_period}
              component={CheckBox}
              label="Período de teste?"
            />
          </Form.Group>
      </Form.Row>
      </div>
      <div className="row flex-row-reverse px-2">
        <div className="btn-group align-self-end">
          <Button className="btn btn-secondary btn-lg font-weight-medium" type="button" onClick={reset} disabled={loading}>
            {loading ? (
              <Spinner />
            ) : (
              "Limpar"
            )}
          </Button>
          <Button className="btn btn-primary btn-lg font-weight-medium" type="submit" disabled={loading}>
            {loading ? (
              <Spinner />
            ) : (
              "Cadastrar"
            )}
          </Button>
        </div>
      </div>
    </Form>
  );
}

export default reduxForm({
  form: "form_create_plan",
  validate
})(CreateForm);