import { Reducer } from 'redux'
import {
  SHOWS_LOAD,
  SHOWS_LOAD_SUCCESS,
  SHOWS_LOAD_ERROR,
  SET_VALUE,
  CLEAR_VALUE,
  SHOWS_CREATE,
  SHOWS_SUCCESS,
  SHOWS_ERROR,
  SHOWS_UPDATE,
  SHOWS_UPDATE_SUCCESS,
  SHOWS_UPDATE_ERROR,
  SHOWS_DELETE,
  SHOWS_DELETE_SUCCESS,
  SHOWS_DELETE_ERROR,
  SHOWS_SETACTIVE,
  SHOWS_SETACTIVE_ERROR,
  SHOWS_SETACTIVE_SUCCESS,
  CATEGORY_SHOWS_LOAD,
  CATEGORY_SHOWS_LOAD_SUCCESS,
  CATEGORY_SHOWS_LOAD_ERROR,
  SCATEGORY_SHOWS_LOAD_ERROR,
  SCATEGORY_SHOWS_LOAD_SUCCESS,
  SCATEGORY_SHOWS_LOAD,
  SHOW_LOAD_ID,
  SHOW_LOAD_ID_SUCCESS,
  SHOW_LOAD_ID_ERROR,
  SHOWS_DASH_LOAD,
  SHOWS_DASH_LOAD_SUCCESS,
  SHOWS_DASH_LOAD_ERROR,
  SHOW_DASH_LOAD_ID_ERROR,
  SHOW_DASH_LOAD_ID,
  SHOW_DASH_LOAD_ID_SUCCESS
} from './types'

const INITIAL_STATE = {
  data: [],
  categories: [],
  scategories: [],
  showSelected: null,
  scategoriesSelected: null,
  newData: false,
  error: false,
  status: null,
  isActive: false,
  loading: false,
  showModalDelete: false,
}

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case SHOWS_LOAD:
      return { ...state, loading: true, error: { loadShow: false }, isActive: false }
    case SHOWS_LOAD_SUCCESS:
      return {
        ...state,
        loading: false,
        error: { loadShow: false },
        data: action.payload.data
      }
    case SHOWS_LOAD_ERROR:
      return {
        ...state,
        loading: false,
        error: { loadShow: true },
        status: action.payload.error
      }

    case CATEGORY_SHOWS_LOAD:
      return { ...state, loading: true, error: { categoryShow: false }, isActive: false }
    case CATEGORY_SHOWS_LOAD_SUCCESS:
      return {
        ...state,
        loading: false,
        error: { categoryShow: false },
        categories: action.payload.data
      }
    case CATEGORY_SHOWS_LOAD_ERROR:
      return {
        ...state,
        loading: false,
        error: { categoryShow: true },
        status: action.payload.error
      }

    case SCATEGORY_SHOWS_LOAD:
      return { ...state, loading: true, error: { scategoryShow: false }, isActive: false }
    case SCATEGORY_SHOWS_LOAD_SUCCESS:
      return {
        ...state,
        loading: false,
        error: { scategoryShow: false },
        scategories: action.payload.data
      }
    case SCATEGORY_SHOWS_LOAD_ERROR:
      return {
        ...state,
        loading: false,
        error: { scategoryShow: true },
        status: action.payload.error
      }

    case SHOW_LOAD_ID:
      return { ...state, loading: true, error: { loadShowId: false }, isActive: false }
    case SHOW_LOAD_ID_SUCCESS:
      return {
        ...state,
        loading: false,
        error: { loadShowId: false },
        showSelected: action.payload.data
      }
    case SHOW_LOAD_ID_ERROR:
      return {
        ...state,
        loading: false,
        error: { loadShowId: true },
        status: action.payload.error
      }

    case SET_VALUE:
      return { ...state, ...action.payload.payload }

    case CLEAR_VALUE:
      return { ...INITIAL_STATE }

    default:
      return state

    case SHOWS_CREATE:
      return { ...state, loading: true, error: false }
    case SHOWS_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        status: action.payload.status,
        newData: true,
      }
    case SHOWS_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
        status: action.payload.error
      }

    case SHOWS_UPDATE:
      return { ...state, loading: true, error: false }
    case SHOWS_UPDATE_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        status: action.payload.status,
        newData: true,
      }
    case SHOWS_UPDATE_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
        status: action.payload.error
      }

    case SHOWS_DELETE:
      return { ...state, loading: true, error: false }
    case SHOWS_DELETE_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        data: action.payload.data,
      }
    case SHOWS_DELETE_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
        status: action.payload.error
      }

    case SHOWS_SETACTIVE:
      return { ...state, loading: true, error: false }
    case SHOWS_SETACTIVE_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        isActive: action.payload.active,
      }
    case SHOWS_SETACTIVE_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
        status: action.payload.error
      }

      case SHOWS_DASH_LOAD:
        return { ...state, loading: { loadShow: true }, error: false, isActive: false }
      case SHOWS_DASH_LOAD_SUCCESS:
        return {
          ...state,
          loading: { loadShow: false },
          error: false,
          data: action.payload.data
        }
      case SHOWS_DASH_LOAD_ERROR:
        return {
          ...state,
          loading: { loadShow: false },
          error: true,
          status: action.payload.error
        }
        
        case SHOW_DASH_LOAD_ID:
          return { ...state, loading: { loadShowId: true }, error:false, isActive: false }
        case SHOW_DASH_LOAD_ID_SUCCESS:
          return {
            ...state,
            loading: { loadShowId: false },
            error: false,
            showSelected: action.payload.data
          }
        case SHOW_DASH_LOAD_ID_ERROR:
          return {
            ...state,
            loading: { loadShowId: false },
            error: true,
            status: action.payload.error
          }
    
  }
}

export default reducer
