import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { createShow, updateShow, clearValue, loadCategoryShow, loadSCategoryShow, loadShowId, setValue } from '../../store/ducks/shows/actions';
import CreateForm from './components/CreateForm';
import { useToast } from '../../_contexts/toast';
import { app } from '../../firebase_config';
import profiles from '../../helpers/models/profiles';
import {convertToFloat, convertToNumber} from '../../helpers/utils';
import validate from './components/validate';
import { SubmissionError } from 'redux-form';


function CreateShow() {
  const { id } = useParams();
  const history = useHistory();
  const dispatch = useDispatch();
  const { showToast } = useToast();
  const { categories, scategories, categorySelected, loading, error, showSelected, newData, status } = useSelector((state) => state.shows);
  const [profileOptions, setProfileOptions] = useState([]);
  const logged_user = JSON.parse(localStorage.getItem('@PermissionYT:user'));
  
  const getUsersByProfile = async (id) => {
		try {
			const res = await app.firestore().collection('users').where("profileId", "==", id && id).get();
			// const query = res.where("profileId", "==", id && id);
			let users = [];
			res.forEach(user => {
				users.push({ id: user.id, ...user.data() })
			});
			// const user = formatUsers(users)
      const formatedUsers = users.map(user=>
        ({value: user.id, label: user.fullName.toUpperCase()})
      )
			setProfileOptions(formatedUsers)


		} catch (err) {
      if(err.response){
      }
			// console.log(err);
		}
	};
  
  const onSubmit = data => {
    const fields = [
      { name: 'title', typeField: 'text', typeValidation: ['required'] },
      { name: 'synopsis', typeField: 'text', typeValidation: ['required'] },
      { name: 'link', typeField: 'text', typeValidation: ['required'] },
      { name: 'trailer', typeField: 'text', typeValidation: ['required'] },
      { name: 'thumbnail', typeField: 'text', typeValidation: ['required'] },
      { name: 'ageRating', typeField: 'text', typeValidation: ['required'] },
      {
        name: 'ageRatingDescription',
        typeField: 'text',
        typeValidation: ['required'],
      },
      // { name: 'cast', typeField: 'text', typeValidation: ['required'] },
      { name: 'production', typeField: 'text', typeValidation: ['required'] },
      { name: 'direction', typeField: 'text', typeValidation: ['required'] },
      { name: 'duration', typeField: 'text', typeValidation: ['required'] },
      { name: 'videoID', typeField: 'text', typeValidation: ['required'] },
      { name: 'videoID2', typeField: 'text', typeValidation: ['required'] },
      // { name: 'category_id', typeField: 'select', typeValidation: ['required'] },
      // {
      //   name: 'category_itens_id',
      //   typeField: 'select',
      //   typeValidation: ['required'],
      // },
    ]

    const errors = validate(data, fields);

    if (Object.keys(errors).length > 0) {
    	throw new SubmissionError(errors);
    }

    if (!logged_user) {
    	throw new SubmissionError(errors);
    }

    const values = {
      title: data.title,
      synopsis: data.synopsis,
      link: data.link,
      trailer: data.trailer,
      thumbnail: data.thumbnail,
      admin_id: profiles[logged_user.profileId].toUpperCase()==='ADMIN'? logged_user.profileId: '', //TODO: Se a pessoa logada for produtora não enviar
      producer_id: profiles[logged_user.profileId].toUpperCase()==='PRODUTORA'? logged_user.profileId: showSelected ? showSelected.producer_id : data.producer_id && data.producer_id.value,
      ageRating: data.ageRating,
      ageRatingDescription: data.ageRatingDescription,
      cast: data.title,
      production: data.production,
      direction: data.direction,
      duration: data.duration,
      videoID: data.videoID,
      videoID2: data.videoID2,
      trending: data.trending ? true : false,
      isActive: data.isActive ? true : false,
      isActiveOnDemand: data.isActiveOnDemand ? true : false,
      price_on_demand: data.price_on_demand?convertToNumber(data.price_on_demand)/100:'',
      isActiveSignature: data.isActiveSignature ? true : false,
      category_id: showSelected && showSelected.category_id || data.category_id ? data.category_id.value : '',
      category_itens_id: showSelected && showSelected.category_itens_id || data.category_itens_id ? data.category_itens_id.map(el=>(el.value)) : []//Envia array de ID's
    }
    const resp = { id, ...values }
    if (!id) {
      dispatch(createShow(values))
    } else {
      dispatch(updateShow(resp))
    }

  };

  useEffect(()=>{
    //Get Lista de Produtoras
    getUsersByProfile('9421e035-1fd6-4918-8fc7-f4e865996bbc');
  },[])


  useEffect(() => {
    if (error === true) {
      showToast('error', status.message)
    } if (newData) {
      showToast('success', status.message)
      dispatch(clearValue())
      history.push("/shows");
    }
  }, [error, newData])

  useEffect(() => {
    dispatch(loadCategoryShow())
  }, [])


  useEffect(() => {
    categorySelected && dispatch(loadSCategoryShow(categorySelected))
    showSelected && showSelected.category_id && id && dispatch(loadSCategoryShow(showSelected.category_id))
  }, [categorySelected, showSelected])

  const categoriesOptions = categories.length ? categories?.map(category => (
    { value: category.id, label: category.name.toUpperCase() }
  )) : [{value: null, label: 'Selecione uma categoria'}]

  const scategoriesOptions = scategories.length ? scategories?.map(scategory => (
    { value: scategory.id, label: scategory.name.toUpperCase() }
  )) : [{value: null, label: 'Selecione uma subcategoria'}]

  useEffect(() => {
    id && dispatch(loadShowId(id))
  }, [id])

  const handleChangeCategory = (data)=>{
    dispatch(setValue({ categorySelected: data.value }))
  }

  const initialValues = id && showSelected ? {
    title: showSelected.title,
    synopsis: showSelected.synopsis,
    link: showSelected.link,
    trailer: showSelected.trailer,
    thumbnail: showSelected.thumbnail,
    producer_id: showSelected.producer_id && profileOptions.find(profile=>profile.value === showSelected.producer_id),
    ageRating: showSelected.ageRating,
    ageRatingDescription: showSelected.ageRatingDescription,
    cast: showSelected.title,
    production: showSelected.production,
    direction: showSelected.direction,
    duration: showSelected.duration,
    videoID: showSelected.videoID,
    videoID2: showSelected.videoID2,
    trending: showSelected.trending,
    isActive: showSelected.isActive,
    isActiveOnDemand: showSelected.isActiveOnDemand,
    price_on_demand: showSelected?.price_on_demand ? Number(convertToFloat(showSelected?.price_on_demand)): '',
    isActiveSignature: showSelected.isActiveSignature,
    category_id: showSelected.category_id && categoriesOptions.find(category=>category.value === showSelected.category_id) || categoriesOptions.find(category=>category.value === '7ca6077d-15d8-4411-a8af-057f5b36c8a1'),
    category_itens_id: showSelected.category_itens_id && scategoriesOptions.filter(c=>showSelected.category_itens_id.includes(c.value))||[]
  } : ""

  return (
    <div className="dashboard align-items-center px-0 lock-full-bg w-80 h-80">
      <div className="row w-100 mx-0">
        <div className="col-md-12 col-lg-12 mx-auto">
          <div className="dashboard px-4 mb-2">
            <h1 className="display-4 text-white font-weight-bold">Cadastrar Espetáculo</h1>
          </div>
          <CreateForm
            onSubmit={onSubmit}
            loading={loading}
            initialValues={initialValues}
            categoriesOptions={categoriesOptions}
            scategoriesOptions={scategoriesOptions}
            profileOptions={profileOptions}
            dispatch={dispatch}
            handleChangeCategory={handleChangeCategory}
            categorySelected={categorySelected}
            logged_user={logged_user}
          />
        </div>
      </div>
    </div>
  );
}

export default CreateShow
