import React from 'react';

import { GrFormSearch } from 'react-icons/gr';
import { BiListPlus } from 'react-icons/bi';
import { Button } from 'react-bootstrap';
import { useHistory } from 'react-router';

function HeaderList() {
    const history = useHistory();
    return (
        <div className="container px-20 m-0 mb-5">
            <div className="row justify-content-between w-100 p-0 m-0">

                <div className="col-sm-12 col-md-5">
                    <h2 className="text-white title-header-table">Gerenciamento de Planos</h2>
                </div>

                <div className="col-sm-12 col-md-4">
                    {/* <div className="input-group h2">
                        <input name="data[search]" className="form-control" id="search" type="text" placeholder="Pesquisar Itens" />
                        <span className="input-group-prepend">
                            <button className="btn btn-primary" type="submit">
                                <span className="glyphicon-search text-white">
                                    <GrFormSearch />
                                </span>
                            </button>
                        </span>
                    </div> */}
                </div>

                <div >
                    <Button className="btn btn-lg h2" onClick={() => history.push('plans/create')}>
                        <BiListPlus className="text-black mr-2" />
                        <span className="text-black font-weight-bold">Cadastrar Planos</span>
                    </Button>
                </div>
            </div>
        </div>
    );
}

export default HeaderList;