import React, { PureComponent } from "react";
import Select from "react-select";

class SelectField extends PureComponent {

	handleChange = selectedOption => {
		const { onChange } = this.props;
		this.setState({ selectedOptionState: selectedOption });
		onChange(selectedOption);

	};

	render() {
		const { value, name, placeholder, options, disabled, isMulti, defaultValue } = this.props;
		const customStyles = {
			option: (provided, state) => ({
				...provided,
				color: state.isSelected ? 'white' : 'black',
				background: state.isSelected ? '#202020' : 'white',
				padding: 20,
				zIndex: 999
			}),
			control: (base, state) => ({
				...base,
				height: 46,
				borderRadius: 3,
				background: 'transparent',

			}),
			singleValue: (provided, state) => ({
				...provided,
				color: 'white',
				transition: 'all 300ms',
				fontWeigth: 600
			}),
			multiValueRemove: (styles, { data }) => ({
				...styles,
				backgroundColor: "white",
				color: '#353535',

				':hover': {
					backgroundColor: "#353535",
					color: 'white',
				},
			}),
		};


		return (
			<Select
				name={name}
				value={value || defaultValue}
				onChange={this.handleChange}
				options={options}
				isDisabled={disabled}
				isMulti={isMulti}
				clearable={false}
				className="react-select"
				placeholder={placeholder}
				classNamePrefix="react-select"
				styles={customStyles}
			/>
		);
	}
}

const renderSelectField = props => {
	const { input, meta, options, placeholder, className, disabled, isMulti, defaultValue } = props;
	return (
		<div className={`form-group`}>
			<SelectField {...input} options={options} placeholder={placeholder} className={`${className}`}
				disabled={disabled}
				isMulti={isMulti}
				defaultValue={defaultValue}
				value={props.input.value}
			/>
			{meta.touched && meta.error && (
				<div className={`tooltip-error ${meta.error ? 'active-tooltip' : ''}`}>{meta.error}</div>
			)}
		</div>
	);
};


export default renderSelectField;