import React from 'react';
import { Button, Form } from 'react-bootstrap';
import { Field, reduxForm } from "redux-form";
import CheckBox from '../../../components/CkeckBox';
import ShowByProfile from '../../../components/ShowByProfile';
import InputCustom from '../../../components/Input';
import renderSelectField from '../../../components/Select';
import profiles from '../../../helpers/models/profiles';
import Spinner from '../../../Layout/Spinner';
import { setValue } from '../../../store/ducks/shows/actions';
import validate from './validate';
import { MONEY } from '../../../components/NumberFormat';

function CreateForm({
  handleSubmit,
  reset,
  loading,
  logged_user,
  categoriesOptions,
  scategoriesOptions,
  profileOptions,
  handleChangeCategory,
  categorySelected,
  initialValues
}) {
  return (
    <Form autoComplete="new-off" className="form" onSubmit={handleSubmit} >
      <div className="card-form pt-5 px-5 mb-5">
        <Form.Row>
          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="title">Título</label>
            <Field
              id="title"
              name="title"
              component={InputCustom}
              className="form-control"
              type="text"
              placeholder="TÍTULO"
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="synopsis">Sinopse</label>
            <Field
              id="synopsis"
              name="synopsis"
              as="textarea"
              rows={3}
              component={InputCustom}
              className="form-control"
              type="text"
              placeholder="SINOPSE"
            />
          </Form.Group>

          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="thumbnail">Thumbnail</label>
            <Field
              id="thumbnail"
              name="thumbnail"
              component={InputCustom}
              className="form-control"
              type="text"
              placeholder="Link da thumbnail"
            />
          </Form.Group>
        </Form.Row>
      </div>
      {/* Seção 2 */}
      <div className="card-form pt-5 px-5 mb-5">
        <Form.Row className="d-flex justify-content-between align-content-center px-5">
          <Form.Group >
            <Field
              id="trending"
              name="trending"
              checked={initialValues.trending}
              component={CheckBox}
              label="Tendência"
            />
          </Form.Group>
          <Form.Group >
            <Field
              id="isActiveOnDemand"
              name="isActiveOnDemand"
              checked={initialValues.isActiveOnDemand}
              component={CheckBox}
              label="OnDemand"
            />
          </Form.Group>
          <Form.Group >
            <Field
              id="isActiveSignature"
              name="isActiveSignature"
              checked={initialValues.isActiveSignature}
              component={CheckBox}
              label="Assinatura"
            />
          </Form.Group>
        </Form.Row>
      </div>

      {/* Seção 3 */}
      <div className="card-form pt-5 px-5 mb-5">
        <Form.Row>
          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="link">Link</label>
            <Field
              id="link"
              name="link"
              component={InputCustom}
              className="form-control "
              type="text"
              placeholder="https://www.produtoravideo.com"
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="trailer">Trailer</label>
            <Field
              id="trailer"
              name="trailer"
              component={InputCustom}
              className="form-control "
              type="text"
              placeholder="https://www.produtoravideo.com"
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-2 col-lg-2">
            <label htmlFor="classificacao">Classificação</label>
            <Field
              id="classificacao"
              name="ageRating"
              component={InputCustom}
              className="form-control"
              type="text"
              placeholder="12 anos"
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-10 col-lg-10">
            <label htmlFor="descricaoClassifica">Descrição da Classificação</label>
            <Field
              id="descricaoClassifica"
              name="ageRatingDescription"
              component={InputCustom}
              as="textarea"
              rows={3}
              className="form-control"
              type="text"
              placeholder="Descrição sobre a classificação..."
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="descricaoClassifica">Preço</label>
            <Field
              id="preco"
              name="price_on_demand"
              component={MONEY}
              className="form-control"
              placeholder="R$ 9,99"
            />
          </Form.Group>
        </Form.Row>
      </div>

      {/* Seção 4 */}
      <div className="card-form pt-5 px-5 mb-5">
        <Form.Row>
          <ShowByProfile role={['ADMIN']}>
          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="producer_id">Produtora</label>
            <Field
              id="producer_id"
              name="producer_id"
              component={renderSelectField}
              className="form-control"
              type="text"
              placeholder="Produtora"
              defaultValue={initialValues.producer_id}
              options={profileOptions}
            />
          </Form.Group>
          </ShowByProfile>

          <Form.Group className="col-12 col-xs-12 col-md-4 col-lg-4">
            <label htmlFor="category_id">Categoria</label>
            <Field
              id="category_id"
              name="category_id"
              component={renderSelectField}
              className="form-control"
              type="text"
              placeholder="Categoria"
              options={categoriesOptions}
              defaultValue={initialValues.category_id}
              onChange={(data) => handleChangeCategory(data)}
            />
          </Form.Group>

          <Form.Group className="col-12 col-xs-12 col-md-8 col-lg-8">
            <label htmlFor="category_itens_id">Sub-Categorias</label>
            <Field
              id="category_itens_id"
              name="category_itens_id"
              component={renderSelectField}
              disabled={Boolean(!initialValues.category_id)}
              isMulti
              defaultValue={initialValues.category_itens_id}
              className="form-control"
              type="text"
              placeholder="Escolha as subcategorias"
              options={scategoriesOptions}
            />
          </Form.Group>
        </Form.Row>
      </div>

      {/* Seção 5 */}
      <div className="card-form pt-5 px-5 mb-5">
        <Form.Row>
          <Form.Group className="col-12 col-xs-12 col-md-5 col-lg-5">
            <label htmlFor="production">Produção</label>
            <Field
              id="production"
              name="production"
              component={InputCustom}
              className="form-control"
              type="text"
              placeholder="PRODUÇÃO"
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-5 col-lg-5">
            <label htmlFor="direction">Direção</label>
            <Field
              id="direction"
              name="direction"
              component={InputCustom}
              className="form-control"
              type="text"
              placeholder="Direção do espetáculo"
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-2 col-lg-2">
            <label htmlFor="duration">Duração</label>
            <Field
              id="duration"
              name="duration"
              component={InputCustom}
              className="form-control"
              type="time"
              placeholder="01:30:00"
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="videoID">Vídeo ID</label>
            <Field
              id="videoID"
              name="videoID"
              component={InputCustom}
              className="form-control"
              type="text"
              placeholder="VÍDEO ID"
            />
          </Form.Group>
          <Form.Group className="col-12 col-xs-12 col-md-12 col-lg-12">
            <label htmlFor="videoID2">Vídeo ID2</label>
            <Field
              id="videoID2"
              name="videoID2"
              component={InputCustom}
              className="form-control"
              type="text"
              placeholder="VÍDEO ID2"
            />
          </Form.Group>
        </Form.Row>
      </div>
      <div className="row flex-row-reverse px-2">
        <div className="btn-group align-self-end">
          <Button className="btn btn-secondary btn-lg font-weight-medium" type="button" onClick={reset} disabled={loading}>
            Limpar
          </Button>
          <Button className="btn btn-primary btn-lg font-weight-medium" type="submit" disabled={loading}>
            {loading ? (
              <Spinner />
            ) : (
              "Cadastrar"
            )}
          </Button>
        </div>
      </div>
    </Form>
  );
}

export default reduxForm({
  form: "form_create_user",
  // validate
})(CreateForm);