import React, { useEffect, useState } from "react";
import { Redirect, Route } from "react-router-dom";
import { useAuth } from "../_contexts/auth";
import api from "../services";
import profiles from "../helpers/models/profiles";


const PrivateRoutes = ({ role, ...rest }) => {
  const [permissions, setPermissions] = useState(false);
  const { userIsAuthenticated, token, userSigned } = useAuth();

  const user = JSON.parse(localStorage.getItem("@PermissionYT:user"));

  // useEffect(() => {
  //   async function loadRoles() {
  //     const response = await api.get("/users/roles");
  //     const findRole = response.data.some((r) =>
  //       role?.split(",").includes(r)
  //     );
  //     setPermissions(findRole);
  //   }

  //   loadRoles();
  // }, []);

  useEffect(() => {
    async function loadRoles() {
      const findRole = role?.split(",").includes(profiles[userSigned.profileId].toUpperCase())
      setPermissions(findRole);
    }

    loadRoles();
  }, [userSigned, role]);


  if (!userIsAuthenticated()) {
    return <Redirect to="/" />;
  }

 if (!role && userIsAuthenticated()) {
    return (
      <Route {...rest} />
    );
  }
   else if(permissions && userIsAuthenticated()){
    return (
      <Route {...rest} />
    );
  }
  else{
    return <Route {...rest} />
  }

};

export default PrivateRoutes;