import { action } from 'typesafe-actions'
import {
  PLANS_LOAD,
  PLANS_LOAD_SUCCESS,
  PLANS_LOAD_ERROR,
  SET_VALUE,
  PLANS_CREATE,
  PLANS_SUCCESS,
  PLANS_ERROR,
  PLANS_UPDATE,
  PLANS_UPDATE_SUCCESS,
  PLANS_UPDATE_ERROR,
  PLANS_DELETE,
  PLANS_DELETE_SUCCESS,
  PLANS_DELETE_ERROR,
  CLEAR_VALUE,
  PLAN_LOAD_ID,
  PLAN_LOAD_ID_SUCCESS,
  PLAN_LOAD_ID_ERROR
} from './types'

export const loadPlan = () => action(PLANS_LOAD)
export const loadPlanSuccess = (data) => action(PLANS_LOAD_SUCCESS, { data })
export const loadPlanFailure = (error) => action(PLANS_LOAD_ERROR, { error })

export const loadPlanId = (data) => action(PLAN_LOAD_ID, { data })
export const loadPlanIdSuccess = (data) => action(PLAN_LOAD_ID_SUCCESS, { data })
export const loadPlanIdFailure = (error) => action(PLAN_LOAD_ID_ERROR, { error })

export const setValue = (payload) => action(SET_VALUE, { payload })
export const clearValue = () => action(CLEAR_VALUE)

export const createPlan = (data) => action(PLANS_CREATE, { data })
export const createPlanSuccess = (status) => action(PLANS_SUCCESS, { status })
export const createPlanFailure = (error) => action(PLANS_ERROR, { error })

export const updatePlan = (data) => action(PLANS_UPDATE, { data })
export const updatePlanSuccess = (status) => action(PLANS_UPDATE_SUCCESS, { status })
export const updatePlanFailure = (error) => action(PLANS_UPDATE_ERROR, { error })

export const deletePlan = (data) => action(PLANS_DELETE, { data })
export const deletePlanSuccess = (data) => action(PLANS_DELETE_SUCCESS, { data })
export const deletePlanFailure = (error) => action(PLANS_DELETE_ERROR, { error })