import React, { useEffect, useState } from 'react';

import Navbar from './Navbar';
import Sidebar from './Sidebar';
import SettingsPanel from './SettingsPanel';
import Footer from './Footer';
import { useAuth } from '../_contexts/auth';
import { useHistory } from 'react-router-dom';

function Layout({ children, history }) {
  const [isFullPageLayout, setIsFullPageLayout] = useState(false);
  const { signOut, userSigned } = useAuth();

  useEffect(() => {
    onRouteChanged();
  }, [history.location])

  function onRouteChanged() {
    const body = document.querySelector('body');
    if (history.location.pathname === '/layout/RtlLayout') {
      body.classList.add('rtl');
    }
    else {
      body.classList.remove('rtl')
    }
    window.scrollTo(0, 0);

    setIsFullPageLayout(true)

    document.querySelector('.page-body-wrapper').classList.add('full-page-wrapper');

  }

  return (
    <div className="container-scroller">
      {isFullPageLayout && <Navbar userSigned={userSigned} signOut={signOut} history={history} />}
      <div className="container-fluid page-body-wrapper">
        {isFullPageLayout && <Sidebar location={history.location} signOut={signOut} />}
        <div className="main-panel">
          <div className="content-wrapper">
            {children}
            {isFullPageLayout && <SettingsPanel />}
          </div>
          {isFullPageLayout && <Footer />}
        </div>
      </div>
    </div>
  );
}

export default Layout;