import React, { createContext, useContext, useEffect, useState } from 'react';
import { toast as Controller, ToastContainer } from 'react-toastify';
import { v4 as uuid } from 'uuid';

const ToastContext = createContext({})


function ToastProvider({ children } ) {
  const [toast, setToast] = useState({type: '', message: ''})

  useEffect(() => {
    const options = {
      position: "top-center",
      autoClose: 3000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true
    }

    if (!toast.message) {
      return
    }

    if (toast.type === 'error') {
      Controller.error(toast.message, options)
      return
    }

    if (toast.type === 'success') {
        Controller.success(toast.message, options)
        return
      }
    
  }, [toast])

  function showToast(type, message) {
    setToast({
      id: uuid(),
      type,
      message
    })
  }

  return (
    <ToastContext.Provider value={{showToast}}>
      <ToastContainer style={{width: '50vw', color:'black !important'}} />
      {children}
    </ToastContext.Provider>
  )
}

function useToast() {
  const context = useContext(ToastContext)
  const { showToast } = context;
  return { showToast };
}

export {
    useToast,
    ToastProvider
};
