export function convertToNumber(number) {
  return String(number).replace(/\D/g, '')
}
export function convertToFloat(number) {
  return String(number).replace(/[^0-9.]/g, '')
}
//
export function currency(number, currency = 'BRL', locale = 'pt-br') {
  return (convertToNumber(number) / 100).toLocaleString(locale, {
    style: 'currency',
    currency: currency,
  })
}

export function dateFormat(dateDb) {
    console.log(dateDb);
  const regex =
    /^([0-9]{4})[-](0[1-9]|1[0-2])[-](0[0-9]|1[0-9]|2[0-9]|3[0-1])/gm

  const [full, year, month, day] = regex.exec(dateDb)
  return {
    full,
    year,
    month,
    day,
  }
}
