const validate = (values) => {
    const errors = {};

    if (!values.name) {
        errors.name = "Informe um nome ";
    }

    if (!values.value) {
        errors.value = "Informe um valor";
    }

    if (!values.period) {
        errors.period = "Informe um periodo em dias";
    }

    return errors;
};

export default validate;