import { set } from "lodash-es";
import React, {
  createContext,
  useCallback,
  useContext, useEffect, useState
} from "react";
import { Redirect, useHistory } from "react-router-dom";
import { app } from "../firebase_config";
import api from "../services";


const AuthContext = createContext({});

const AuthProvider = ({ children }) => {
  const history = useHistory();
  const [token, setToken] = useState(() => {
    const token = localStorage.getItem("@PermissionYT:token");

    if (token) {

      return token;
    }

    return null;
  });
  const [user, setUser] = useState(null);
  // const [userIsAuthenticated, setUserIsAuthenticated] = useState(false);
  const [userSigned, setUserSigned] = useState(() => {
    const user = localStorage.getItem("@PermissionYT:user");

    if (user) {
      return JSON.parse(user);;
    }

    return null;
  });
  const [refreshToken, setRefreshToken] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  useEffect(() => {
    if (user) {
      getTokenFirebase()
    }
  }, [user]);

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("@PermissionYT:user"));
    setUserSigned(user);
  }, [])

  const userIsAuthenticated = useCallback(() => {
    const tk = localStorage.getItem("@PermissionYT:token");
    const rtk = localStorage.getItem("@PermissionYT:refreshToken");

    if (tk) {
      return true
    } else {
      return false
    }
  }, [token]);
  const getTokenFirebase = async() => {
    if (user) {
      const userId = app.auth().currentUser.uid;
      try {
        const res = await app.firestore().collection('users').doc(userId).get()
        const userNotIsclient = res && ['d5897119-5673-4417-b09d-79c278bbc134','9421e035-1fd6-4918-8fc7-f4e865996bbc'].includes(res.data()?.profileId)
        if(userNotIsclient){
          setUserSigned({id: userId, ...res.data()});
          localStorage.setItem("@PermissionYT:user", JSON.stringify({id: userId, ...res.data()}));

          app.auth().currentUser.getIdToken().then(res => {
            // console.log('firebaseToken', res)
            tokenApi(res)
            // setToken(res)

          })
            .catch(error => {
              if(error.response){
                setLoading(false)
                setError(true)
                console.log(error.response.message);
              }

            });
        }else{
          setLoading(false)
          setError(true)
        }

      } catch (err) {
        console.log(err);
        setLoading(false)
        setError(true)
      }
    }
  }
  // const getTokenFirebase = () => {
  //   if (user) {
  //     app.auth().currentUser.getIdToken().then(res => {
  //       // console.log('firebaseToken', res)
  //       tokenApi(res)
  //       // setToken(res)

  //     })
  //       .catch(error => {
  //         if(error.response){
  //           setLoading(false)
  //           setError(true)
  //         }

  //       });
  //   }
  // }

  // const setUserFirebaseToStorage = async () => {
  //   if (user) {
  //     const userId = app.auth().currentUser.uid;
  //     try {
  //       const res = await app.firestore().collection('users').doc(userId).get()
  //       setUserSigned(res.data());
  //       localStorage.setItem("@PermissionYT:user", JSON.stringify(res.data()));
  //     } catch (err) {
  //       console.log(err);
  //     }
  //   }
  // }


  const tokenApi = (res) => {
    if (user) {
      api.post('/auth', {}, {
        headers: {
          webstage_auth: process.env.REACT_APP_API_SECRET_CODE, 
          Authorization: `Bearer ${res}`
        }
      }).then(tokenapi => {
        setToken(tokenapi.data.body.accessToken);

        localStorage.setItem("@PermissionYT:token", tokenapi?.data?.body?.accessToken);
        // setUserFirebaseToStorage();
        localStorage.setItem("@PermissionYT:refreshToken", tokenapi?.data?.body?.refreshToken);
        setLoading(false);
        setError(false)
      }).catch(err => {
        if(err.response){
        setLoading(false)
        setError(true)   
        }

      })
    }

  }


  const signOut = () => {
    const token = localStorage.getItem('@PermissionYT:token')
    if (token) {
      app.auth().signOut()
      localStorage.clear()
      // history.push('/dashboard')
    }
  }



  return (
    <AuthContext.Provider value={
      {
        user,
        setUser,
        loading,
        setLoading,
        error,
        userIsAuthenticated,
        signOut,
        token,
        refreshToken,
        userSigned,
        setError
      }
    }>
      {children}
    </AuthContext.Provider>
  );
};

function useAuth() {
  const context = useContext(AuthContext);
  return context;
}

export { AuthProvider, useAuth };
