import React from 'react';
import profiles from '../../helpers/models/profiles';

function ShowByProfile({children, role}) {
    const logged_user = JSON.parse(localStorage.getItem('@PermissionYT:user'));
  return(
    <>
    {role.includes(profiles[logged_user.profileId].toUpperCase())&& (
        children
    )}
    </>
  );
}

export default ShowByProfile;