import React, { useEffect } from "react";
import { Button, ButtonGroup, Form, FormCheck, OverlayTrigger, Tooltip } from "react-bootstrap";
import { MdOutlineDeleteOutline, MdOutlineEdit } from 'react-icons/md';
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { Col, Row } from "reactstrap";
import Switch from "../../components/Switch";
import TableCustom from "../../components/Table";
import { app } from "../../firebase_config";
import profiles from "../../helpers/models/profiles";
import { loadShow, setValue, updateSetShow } from "../../store/ducks/shows/actions";
import { useAuth } from "../../_contexts/auth";
import DeleteForm from "./components/DeleteForm";
import HeaderList from "./components/HeaderList";


function Report() {
	const history = useHistory();
	const dispatch = useDispatch();
	const { data, loading, isActive } = useSelector((state) => state.shows);
	const logged_user = JSON.parse(localStorage.getItem('@PermissionYT:user'));
	const isProdutora = profiles[logged_user.profileId].toUpperCase()==='PRODUTORA';

	const handleDelete = (show) => {
		dispatch(setValue({ showModalDelete: true, showSelected: show }));
	}

	const handleEdit = (show) => {
		history.push(`/shows/edit/${show.id}`)
		dispatch(setValue({ showSelected: show }));
	}


	const handleCheckActive = (e, show) => {
		dispatch(updateSetShow({ value: e, show }));
	}
	const columns =
		[
			{
				Header: "Título",
				accessor: "title"
			},
			{
				Header: "Artista",
				accessor: "artista"
			},
			{
				Header: 'Ações',
				cell: (show) => {
					return (
						<div style={{display: 'flex', flexDirection:'row', gap: 8, alignItems: 'center'}}>

						<ButtonGroup>
							<Button
								variant="secondary"

								onClick={() => handleEdit(show)}
								className="d-flex font-weight-bold text-white btn btn-xs"
							>
								<MdOutlineEdit className="text-white font-weight-bold mr-1" />
								<span className="text-white font-weight-bold mr-1">
									Editar
								</span>
							</Button>
							<Button
								variant="warning"
								onClick={() => handleDelete(show)}
								className="d-flex text-black font-weight-bold btn btn-xs"
							>
								<MdOutlineDeleteOutline className="text-black font-weight-bold mr-1" />
								<span className="text-black font-weight-bold mr-1">
									Excluir
								</span>
							</Button>

						</ButtonGroup>
						
						{/* <Form.Check
								type="checkbox"
								id="custom-switch"
								label="Ativar"
								onChange={e => handleCheckActive(e, show)}
								checked={isActive || show.isActive}
							/> */}
							<div style={{display: 'flex', flexDirection:'column'}}>
									<OverlayTrigger
									placement='top'
									overlay={
										<Tooltip id={`tooltip-produtora`}>
										{`${isActive || show.isActive?'Desativar':'Ativar'}`} <strong>espetáculo</strong>.
										</Tooltip>
									}
									>
									<Button style={{ background: 'transparent',
														border: 'transparent',
														height: '5px',
														width: '100%',
														padding: 0,
														margin:0}}></Button>

									</OverlayTrigger>
									<Switch
									onChange={e => handleCheckActive(e, show)}
									defaultValue={isActive || show.isActive}
									/>
								</div>
						</div>
					);
				},
			},
		]
	useEffect(() => dispatch(loadShow(isProdutora && {userId:logged_user.id})), [])


	return (
		<div className="dashboard">
			<Row>
				<Col md={12} className="table-responsive col-md-12">
					<HeaderList />

					<TableCustom
						noData="Nenhum registro encontrado"
						data={data}
						loading={loading}
						columns={columns}
					/>
				</Col>
			</Row>
			<DeleteForm />
		</div>

	);
}

export default Report;