import React from "react";
import NumberFormat from "react-number-format";

export default function NumberFormatCustom(props) {
	const {
		inputRef,
		placeholder,
		type,
		input: { onChange, ...input },
		meta: { touched, error },
		...other
	} = props;

	return (
		<div className="form__form-group-input-wrap">
			<NumberFormat
				{...other}
				{...input}
				getInputRef={inputRef}
				placeholder={placeholder}
				type={type}
				onValueChange={values => {
					onChange(values.value);
				}}
			/>
			{touched && error && (
				<div className={`tooltip-error ${error ? 'active-tooltip' : ''}`}>{error}</div>
			)}
		</div>
	);
}

export function NumberFormatCustomFloat(props) {
	const {
		inputRef,
		placeholder,
		type,
		input: { onChange, ...input },
		meta: { touched, error },
		...other
	} = props;

	return (
		<div className="form__form-group-input-wrap">
			<NumberFormat
				{...other}
				{...input}
				getInputRef={inputRef}
				placeholder={placeholder}
				type={type}
				onValueChange={values => onChange && onChange(values.formattedValue)}
			/>
			{touched && error && (
				<div className={`tooltip-error ${error ? 'active-tooltip' : ''}`}>{error}</div>
			)}
		</div>
	);
}

export const CPF = props => (
	<NumberFormatCustom {...props} format="###.###.###-##" />
);

export const CNPJ = props => (
	<NumberFormatCustom {...props} format="##.###.###/####-##" />
);

export const CPFCNPJ = props => (
	<NumberFormatCustom
		{...props}
		format={
			props.input.value && props.input.value.match(/\d/g).join("").length <= 11
				? "###.###.###-###"
				: "##.###.###/####-##"
		}
	/>
);

export const IE = props => (
	<NumberFormatCustom {...props} format="###.###.###" />
);

export const CEP = props => (
	<NumberFormatCustom {...props} format="#####-###" />
);

export const PHONE = props => {
	return <NumberFormatCustom {...props} format="(##) ####-#####" />;
};

export const MONEY = props => {
	return (
		<NumberFormatCustomFloat
			prefix={"R$ "}
			decimalSeparator=","
			thousandSeparator="."
			decimalScale={2}
			fixedDecimalScale
			{...props}
		/>
	);
};

export const PERCENTAGE = props => {
	return (
		<NumberFormatCustomFloat
			suffix={"%"}
			decimalSeparator=","
			thousandSeparator="."
			decimalScale={1}
			fixedDecimalScale
			{...props}
		/>
	);
};

export const PERCENTAGE2 = props => {
	return (
		<NumberFormatCustomFloat
			suffix={"%"}
			decimalSeparator=","
			thousandSeparator="."
			decimalScale={2}
			fixedDecimalScale
			{...props}
		/>
	);
};

export const DATE = props => (
	<NumberFormatCustom {...props} format="##/##/####" />
);