import React from "react";
import { Route, Switch, useHistory, HashRouter } from "react-router-dom";
import Layout from "../Layout";
import Login from "../pages/Account/Login";
import Dashboard from "../pages/Dashboard";
import CreateShow from "../pages/Shows/CreateShow";
import ReportShow from "../pages/Shows/Report";
import CreatePlan from "../pages/Plans/CreatePlan";
import ReportPlan from "../pages/Plans/Report";
import CreateUsers from "../pages/Users/CreateUsers";
import EditUsers from "../pages/Users/EditUsers";
import Report from "../pages/Users/Report";
import PrivateRoutes from "./PrivateRoutes";
import EditProdutora from "../pages/Users/EditProdutora";
import Page404 from "../pages/Errors/Page404";

const Routes = () => {
  const history = useHistory();
  return (
    <HashRouter>
    <Switch>
      <Route path="/" exact component={Login} />
      <Layout history={history}>
        <PrivateRoutes exact path="/users" component={Report} role="ADMIN"/>
        <PrivateRoutes path={`/users/edit/:id`} component={EditUsers} role="ADMIN" />
        <PrivateRoutes path={`/personal-data`} component={EditProdutora} role="PRODUTORA" />
        <PrivateRoutes path="/users/create" component={CreateUsers} role="ADMIN" />

        <PrivateRoutes exact path="/shows" component={ReportShow} role="ADMIN,PRODUTORA" />
        <PrivateRoutes path="/shows/create" component={CreateShow} role="ADMIN,PRODUTORA" />
        <PrivateRoutes path={`/shows/edit/:id`} component={CreateShow} role="ADMIN,PRODUTORA" />

        <PrivateRoutes exact path="/plans" component={ReportPlan} role="ADMIN"/>
        <PrivateRoutes path="/plans/create" component={CreatePlan} role="ADMIN"/>
        <PrivateRoutes path={`/plans/edit/:id`} component={CreatePlan} role="ADMIN"/>

        {/* registrar as rotas no navbar */}
        <PrivateRoutes exact path="/dashboard" component={Dashboard} role="ADMIN,PRODUTORA"/>
        <PrivateRoutes exact path="/not-access" component={Page404}/>
        {/* <Route path="/dashboard" component={Users} /> */}
      </Layout>

      {/* <PrivateRoutes
        path="/list"
        component={List}
        role="ROLE_ADMIN,ROLE_USER"
      /> */}
    </Switch>
    </HashRouter>
  );
};

export default Routes;