import { Reducer } from 'redux'
import {
  PLANS_LOAD,
  PLANS_LOAD_SUCCESS,
  PLANS_LOAD_ERROR,
  SET_VALUE,
  PLANS_CREATE,
  PLANS_SUCCESS,
  PLANS_ERROR,
  PLANS_UPDATE,
  PLANS_UPDATE_SUCCESS,
  PLANS_UPDATE_ERROR,
  PLANS_DELETE,
  PLANS_DELETE_SUCCESS,
  PLANS_DELETE_ERROR,
  CLEAR_VALUE,
  PLAN_LOAD_ID,
  PLAN_LOAD_ID_ERROR,
  PLAN_LOAD_ID_SUCCESS
} from './types'

const INITIAL_STATE = {
  data: [],
  planSelected: false,
  newData: false,
  error: false,
  status: null,
  loading: false,
  planModalDelete: false,
}

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case PLANS_LOAD:
      return { ...state, loading: true, error: { loadPlan: false } }
    case PLANS_LOAD_SUCCESS:
      return {
        ...state,
        loading: false,
        error: { loadPlan: false },
        data: action.payload.data,
      }
    case PLANS_LOAD_ERROR:
      return {
        ...state,
        loading: false,
        error: { loadPlan: true },
        status: action.payload.error
      }
    case PLAN_LOAD_ID:
      return { ...state, loading: true, error: { loadPlanId: false }, isActive: false }
    case PLAN_LOAD_ID_SUCCESS:
      return {
        ...state,
        loading: false,
        error: { loadPlanId: false },
        planSelected: action.payload.data
      }
    case PLAN_LOAD_ID_ERROR:
      return {
        ...state,
        loading: false,
        error: { loadPlanId: true },
        status: action.payload.error
      }


    case SET_VALUE:
      return { ...state, ...action.payload.payload }


    case CLEAR_VALUE:
      return { ...INITIAL_STATE }

    default:
      return state

    case PLANS_CREATE:
      return { ...state, loading: true, error: false }
    case PLANS_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        status: action.payload.status,
        newData: true,
      }
    case PLANS_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
        status: action.payload.error
      }

    case PLANS_UPDATE:
      return { ...state, loading: true, error: false }
    case PLANS_UPDATE_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        status: action.payload.status,
        newData: true,
      }
    case PLANS_UPDATE_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
        status: action.payload.error
      }

    case PLANS_DELETE:
      return { ...state, loading: true, error: false }
    case PLANS_DELETE_SUCCESS:
      return {
        ...state,
        loading: false,
        error: false,
        data: action.payload.data,
      }
    case PLANS_DELETE_ERROR:
      return {
        ...state,
        loading: false,
        error: true,
        status: action.payload.error
      }
  }
}

export default reducer
