import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { clearValue, createPlan as create, loadPlanId, updatePlan } from '../../store/ducks/plans/actions';
import CreateForm from './components/CreateForm';
import validate from '../Plans/components/validate';
import { SubmissionError } from 'redux-form';
import { convertToNumber } from '../../helpers/utils';
import { status } from '../../helpers/http-status/http-status';
import { useToast } from '../../_contexts/toast';

function CreatePlan() {
  const { id } = useParams();
  const history = useHistory();
  const dispatch = useDispatch();
  const { showToast } = useToast();
  const state = useSelector((state) => state.plans);

  const onSubmit = data => {
    const errors = validate(data);


    if (Object.keys(errors).length > 0) {
      throw new SubmissionError(errors);
    }
    const values = {
      name: data.name,
      value: convertToNumber(data.value) / 100,
      is_test_period: data.is_test_period || false,
      period: Number(data.period) || 0,
      order: Number(data.order) || 0
    }
    const resp = { id, ...values }


    if (!id) {
      dispatch(create(values))
    } else {
      dispatch(updatePlan(resp))
    }

  };

  useEffect(() => {
    if (state?.error === true) {
      showToast('error', state.status.message)
    } if (state?.newData) {
      showToast('success', state.status.message)
      dispatch(clearValue())
      history.push("/plans");
    }
  }, [state.error, state.newData])


  useEffect(() => {
    id && dispatch(loadPlanId(id))
  }, [id])

  // TODO: Pegar initialValues da API pelo id

  const initialValues = id && state.planSelected ? {
    name: state.planSelected.name,
    value: state.planSelected.value.split('$')[1],
    is_test_period: state.planSelected.is_test_period,
    period: state.planSelected.period,
    order: state.planSelected.order

  } : ""

  return (
    <div className="dashboard align-items-center px-0 lock-full-bg w-80 h-80">
      <div className="row w-100 mx-0">
        <div className="col-md-12 col-lg-12 mx-auto">
          <div className="dashboard px-4 mb-2">
            <h1 className="display-4 text-white font-weight-bold">{id ? 'Editar' : 'Cadastrar'} Plano</h1>
          </div>
          <CreateForm
            onSubmit={onSubmit}
            loading={state.loading}
            initialValues={initialValues}
          />
        </div>
      </div>
    </div>
  );
}

export default CreatePlan
