import { all, takeLatest, takeEvery } from 'redux-saga/effects'
import {
  loadPlan,
  createPlan,
  deletePlan,
  updatePlan,
  loadPlanId,
} from './plans/sagas'
import {
  PLANS_CREATE,
  PLANS_DELETE,
  PLANS_LOAD,
  PLANS_UPDATE,
  PLAN_LOAD_ID,
} from './plans/types'
import {
  createShow,
  deleteShow,
  loadCategoryShow,
  loadSCategoryShow,
  loadShow,
  loadShowId,
  updateShow,
  setShowActive,
  loadShowDash,
  loadShowDashId,
} from './shows/sagas'
import {
  CATEGORY_SHOWS_LOAD,
  SCATEGORY_SHOWS_LOAD,
  SHOWS_CREATE,
  SHOWS_DELETE,
  SHOWS_LOAD,
  SHOWS_UPDATE,
  SHOW_LOAD_ID,
  SHOWS_SETACTIVE,
  SHOWS_DASH_LOAD,
  SHOW_DASH_LOAD_ID,
} from './shows/types'

export default function* rootSaga() {
  return yield all([
    // takeLatest(CREATE_USER, createUser),
    // takeLatest(LOAD_USER, loadUser)

    takeLatest(SHOWS_LOAD, loadShow),
    takeEvery(SHOW_LOAD_ID, loadShowId),
    takeEvery(CATEGORY_SHOWS_LOAD, loadCategoryShow),
    takeEvery(SCATEGORY_SHOWS_LOAD, loadSCategoryShow),
    takeLatest(SHOWS_CREATE, createShow),
    takeLatest(SHOWS_DELETE, deleteShow),
    takeLatest(SHOWS_UPDATE, updateShow),
    takeEvery(SHOWS_SETACTIVE, setShowActive),

    takeLatest(SHOWS_DASH_LOAD, loadShowDash),
    takeEvery(SHOW_DASH_LOAD_ID, loadShowDashId),

    takeLatest(PLANS_LOAD, loadPlan),
    takeEvery(PLAN_LOAD_ID, loadPlanId),
    takeLatest(PLANS_CREATE, createPlan),
    takeLatest(PLANS_DELETE, deletePlan),
    takeEvery(PLANS_UPDATE, updatePlan),
  ])
}
