import React from 'react';
import { Button, Form, Tooltip, OverlayTrigger } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Field, reduxForm } from "redux-form";
import InputCustom from '../../../components/Input';
import { CNPJ, DATE, PHONE } from '../../../components/NumberFormat';
import renderSelectField from '../../../components/Select';
import Spinner from '../../../Layout/Spinner';
import { BiPlus } from 'react-icons/bi';
import validate from './validate';
import ShowByProfile from '../../../components/ShowByProfile';
import Switch from '../../../components/Switch';

function CreateForm({
  handleSubmit,
  reset,
  loading,
  initialValues,
  profilesOptions,
  handleProfileSelected,
  profileSelected,
  categoriesOptions,
  handleProducerBank,
  producerBankData,
  addProducerBank,
  changeEmailPass,
  isChangePass
}) {
  return (
    <Form autoComplete="new-off" className="pt-3" onSubmit={handleSubmit} >
      <div className="card-form pt-5 px-5 mb-5">
        <ShowByProfile role={['PRODUTORA']}>
          <Form.Row>
            <Form.Group className="col-md-6">
              <label htmlFor="email">Email</label>
              <Field
                id="email"
                name="email"
                component={InputCustom}
                className="form-control"
                type="text"
                disabled="true"
                placeholder="email"
              />
            </Form.Group>
            <Form.Group className="col-md-6">
              <label htmlFor="password">Senha</label>
              <Field
                id="password"
                name="password"
                component={InputCustom}
                className="form-control"
                type="password"
                disabled="true"
                placeholder="SENHA"
              />
              <label>**Esses dados só podem ser alterados por um ADM, entre em contato.</label>

            </Form.Group>

          </Form.Row>
        </ShowByProfile>
        <ShowByProfile role={['ADMIN']}>
          <Form.Row>
          <div style={{display: 'flex', flexDirection:'column'}}>
									<OverlayTrigger
									placement='top'
									overlay={
										<Tooltip id={`tooltip-produtora`}>
										<strong>Editar Email e Senha</strong>?
										</Tooltip>
									}
									>
									<Button style={{ background: 'transparent',
														border: 'transparent',
														height: '5px',
														width: '100%',
														padding: 0,
														margin:0}}></Button>

									</OverlayTrigger>
									<Switch
									onChange={(e)=>changeEmailPass(e)}
									/>
								</div>
          </Form.Row>
          <Form.Row>
            <Form.Group className="col-md-6">
              <label htmlFor="email">Email</label>
              <Field
                id="email"
                name="email"
                component={InputCustom}
                className="form-control"
                type="text"
                disabled={isChangePass?false:true}
                placeholder="email"
              />
            </Form.Group>
            <Form.Group className="col-md-6">
              <label htmlFor="password">Senha</label>
              <Field
                id="password"
                name="password"
                component={InputCustom}
                className="form-control"
                disabled={isChangePass?false:true}
                type="password"
                placeholder="SENHA"
              />
              <label>**Ative ou desative a opção a cima para alterar email e senha.</label>

            </Form.Group>

          </Form.Row>
        </ShowByProfile>


      </div>
      <div className="card-form pt-5 px-5 mb-5">
        <Form.Row>
          <Form.Group className="col-md-5">
            <label htmlFor="fullName">Nome</label>
            <Field
              id="fullName"
              name="fullName"
              component={InputCustom}
              className="form-control "
              type="text"
              placeholder="NOME COMPLETO"
            />
          </Form.Group>
          <Form.Group className="col-md-4">
            <label htmlFor="phone">Telefone</label>
            <Field
              id="phone"
              name="phone"
              component={PHONE}
              className="form-control"
              type="text"
              placeholder="TELEFONE"
            />
          </Form.Group>
          <Form.Group className="col-md-3">
            <label htmlFor="birthday">Data de Aniversário</label>
            <Field
              id="birthday"
              name="birthday"
              component="input"
              className="form-control"
              type="date"
              placeholder="Data de Aniversário"
            />
          </Form.Group>
        </Form.Row>
      </div>
      <div className="card-form pt-5 px-5 mb-5">
        <Form.Row>
          <Form.Group className="col-md-12">
            <label htmlFor="profile">Perfil</label>
            <ShowByProfile role={['PRODUTORA']}>
              <Field
                id="profile"
                name="profile"
                component={renderSelectField}
                className="form-control"
                type="text"
                placeholder="Selecione um Perfil"
                defaultValue={profilesOptions[1]}
                options={profilesOptions}
                disabled={true}
                onChange={(e) => handleProfileSelected(e)}
              />
            </ShowByProfile>
            <ShowByProfile role={['ADMIN']}>
              <Field
                id="profile"
                name="profileId"
                component={renderSelectField}
                className="form-control"
                type="text"
                placeholder="SELECIONE UM PERFIL"
                defaultValue={initialValues}
                options={profilesOptions}
                onChange={(e) => handleProfileSelected(e)}
              />
            </ShowByProfile>

          </Form.Group>
        </Form.Row>

      </div>
      {profileSelected && profileSelected?.label === 'Produtora' ? (
        <>
          <div className="card-form pt-5 px-5 mb-5">
            <Form.Group className="col-md-9">
              <label>Dados da Produtora</label>
            </Form.Group>
          </div>
          <div className="card-form pt-5 px-5 mb-5">
            <Form.Row>
              <Form.Group className="col-md-5">
                <label htmlFor="category">Categoria</label>
                <Field
                  id="category"
                  name="category"
                  component={renderSelectField}
                  className="form-control"
                  type="text"
                  options={categoriesOptions}
                  defaultValue={initialValues.category}
                  placeholder="Selecione uma categoria"
                />
              </Form.Group>
              <Form.Group className="col-md-7">
                <label htmlFor="description">Descrição</label>
                <Field
                  id="description"
                  name="description"
                  component={InputCustom}
                  as="textarea"
                  rows={3}
                  className="form-control"
                  type="text"
                  placeholder="Descrição da produtora..."
                />
              </Form.Group>
              <Form.Group className="col-md-12">
                <label htmlFor="urlImage">Imagem</label>
                <Field
                  id="urlImage"
                  name="thumbnail"
                  component={InputCustom}
                  className="form-control"
                  type="text"
                  placeholder="URL de Foto ou logomarca da produtora..."
                />
              </Form.Group>
            </Form.Row>

          </div>
          <div className="card-form pt-5 px-5 mb-5">
            <Form.Row>
              <Form.Group className="col-md-9">
                <label>Redes Sociais</label>
              </Form.Group>
            </Form.Row>
            <Form.Row>
              <Form.Group className="col-md-12">
                <label htmlFor="rdInstagram">Instagram</label>
                <Field
                  id="rdInstagram"
                  name="rdInstagram"
                  component={InputCustom}
                  className="form-control"
                  type="text"
                  placeholder="@produtora"
                />
              </Form.Group>
              <Form.Group className="col-md-12">
                <label htmlFor="rdFacebook">Facebook</label>
                <Field
                  id="rdFacebook"
                  name="rdFacebook"
                  component={InputCustom}
                  className="form-control"
                  type="text"
                  placeholder="https://www.facebook.com/produtora"
                />
              </Form.Group>
              <Form.Group className="col-md-12">
                <label htmlFor="rdSite">Site</label>
                <Field
                  id="rdSite"
                  name="rdSite"
                  component={InputCustom}
                  className="form-control"
                  type="text"
                  placeholder="https://www.produtora.com"
                />
              </Form.Group>
            </Form.Row>

          </div>
          <div className="card-form pt-5 px-5 mb-5">
            <Form.Row>
              <Form.Group className="col-md-9">
                <label>Dados Bancários</label>
              </Form.Group>
            </Form.Row>
            {producerBankData.map((producer, index) => (
              <div className='' key={index}>
                <Form.Row>
                  <Form.Group className="col-md-3">
                    <label htmlFor="bank">Banco</label>
                    <input
                      className="form-control"
                      value={producer['bank']}
                      name={'bank'}
                      onChange={(e) => handleProducerBank(e, index)}
                      type="text" />
                  </Form.Group>
                  <Form.Group className="col-md-4">
                    <label htmlFor="bankBranch">Agência</label>
                    <input
                      name={'bankBranch'}
                      value={producer['bankBranch']}
                      className="form-control"
                      type="text"
                      onChange={(e) => handleProducerBank(e, index)}
                    />
                  </Form.Group>
                  <Form.Group className="col-md-5">
                    <label htmlFor="account">Conta</label>
                    <input
                      name={'account'}
                      value={producer['account']}
                      className="form-control"
                      type="text"
                      onChange={(e) => handleProducerBank(e, index)}
                    />
                  </Form.Group>
                </Form.Row>
                <Form.Row>
                  <Form.Group className="col-md-3">
                    <label htmlFor="pixType">Tipo de Pix</label>
                    <input
                      name={'pixType'}
                      value={producer['pixType']}
                      className="form-control"
                      type="text"
                      onChange={(e) => handleProducerBank(e, index)}
                    />
                  </Form.Group>
                  <Form.Group className="col-md-9">
                    <label htmlFor="pixNumber">Chave Pix</label>
                    <input
                      name={'pixNumber'}
                      value={producer['pixNumber']}
                      className="form-control"
                      type="text"
                      onChange={(e) => handleProducerBank(e, index)}
                    />
                  </Form.Group>
                </Form.Row>
              </div>
            ))}
            <div className="d-flex justify-content-center mb-3">
              <Button className="btn btn-primary " style={{ borderRadius: '50%', height: '50px', width: '50px' }} disabled={loading || producerBankData.length === 5} type="button" onClick={addProducerBank}>
                <BiPlus size={22} />
              </Button>
            </div>
          </div>
          <div className="card-form pt-5 px-5 mb-5">
            <Form.Row>
              <Form.Group className="col-md-5">
                <label htmlFor="cnpj">CNPJ</label>
                <Field
                  id="cnpj"
                  name="cnpj"
                  component={CNPJ}
                  className="form-control"
                  type="text"
                />
              </Form.Group>
              <Form.Group className="col-md-7">
                <label htmlFor="companyName">Nome Comercial</label>
                <Field
                  id="companyName"
                  name="companyName"
                  component={InputCustom}
                  className="form-control"
                  type="text"
                />
              </Form.Group>
            </Form.Row>

          </div>
        </>
      ) : ''}
      <div className="row flex-row-reverse px-2">
        <div className="btn-group align-self-end">
          <Button className="btn btn-secondary btn-lg font-weight-medium" type="button" onClick={reset} disabled={loading}>
            {loading ? (
              <Spinner />
            ) : (
              "Limpar"
            )}
          </Button>
          <Button className="btn btn-primary btn-lg font-weight-medium" type="submit" disabled={loading}>
            {loading ? (
              <Spinner />
            ) : (
              "Cadastrar"
            )}
          </Button>
        </div>
      </div>
    </Form>
  );
}

export default reduxForm({
  form: "form_create_user",
})(CreateForm);