import {
  CREATE_USER, CREATE_USER_ERROR, CREATE_USER_SUCCESS, LOAD_USER, LOAD_USER_ERROR, LOAD_USER_SUCCESS, SET_VALUE
} from './types'

const INITIAL_STATE = {
  data: [],
  userSelected: null,
  error: false,
  loading: false,
  filter: '',
  showModalDelete: false,
}

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case LOAD_USER:
      return { ...state, loading: { load: true }, filter: 'all' }
    case LOAD_USER_SUCCESS:
      return {
        ...state,
        loading: { load: false },
        error: { load: false },
        data: action.payload.users,
      }
    case LOAD_USER_ERROR:
      return {
        ...state,
        loading: { load: false },
        error: { load: true },
      }
    case CREATE_USER:
      return { ...state, loading: { create: true }, filter: 'all' }
    case CREATE_USER_SUCCESS:
      return {
        ...state,
        loading: { create: false },
        error: { create: false },
      }
    case CREATE_USER_ERROR:
      return {
        ...state,
        loading: { create: false },
        error: { create: true },
      }

    case SET_VALUE:
      return { ...state, ...action.payload.payload }

    default:
      return state
  }
}

export default reducer
