import React from 'react';
import { FaTheaterMasks } from 'react-icons/fa';
import { RiVipCrownFill } from 'react-icons/ri';
import profiles from '../../helpers/models/profiles';
const profileIcons = {
  ADMIN: <RiVipCrownFill color='white'/>,
  PRODUTORA:<FaTheaterMasks color='white'/>
}
function ShowProfile({profileId}) {
  return(
    <div className='d-flex'>
      {profileIcons[profiles[profileId].toUpperCase()]}
      <span className={`badge ${profiles[profileId].toUpperCase()==='ADMIN'? 'badge-warning':'badge-secondary text-white'} ml-1`}>
        {profiles[profileId]}
      </span>
    </div>
  );
}

export default ShowProfile;