import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Modal } from '../../../components/Modal';
import { app } from '../../../firebase_config';
import { setValue, deleteShow } from '../../../store/ducks/shows/actions';
import { useToast } from '../../../_contexts/toast';

// import { Container } from './styles';

const DeleteForm = () => {
    const dispatch = useDispatch();
    const { showModalDelete, showSelected, loading } = useSelector((state) => state.shows);
    const handleClose = () => {
        dispatch(setValue({ showModalDelete: false }));
    }
    const handleDelete = () => {
        dispatch(deleteShow(showSelected.id));
        handleClose()
    }
    return (
        <Modal
            modalTitle="Deletar Usuário"
            buttonTitle="Confirmar"
            buttonColor="primary"
            show={showModalDelete}
            setShow={handleClose}
            setConfirm={handleDelete}
        >
            <p className="text-white font-weight-bold">Deseja realmente excluir o espetáculo</p>
        </Modal>
    );
}

export default DeleteForm;